import React from 'react';
import {
  Form
} from 'reactstrap';

import {
  compose
} from 'recompose';

export default compose(

)((props) => (
  <Form {...props} onSubmit={function(event) {
    event.preventDefault();
    props.onSubmit.apply(this, arguments);
  }} />
))


