import React from 'react';
import {
  Button,
  Input,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Col,
  FormGroup,
  Label
} from 'reactstrap';
import { compose, withHandlers } from 'recompose';
import ControlForm from '../_components/HOC/ControlForm';
import ControlSpinner from '../_components/HOC/ControlSpinner';
import { Spinner } from '../_components/common';
import toast from '../utils/toast';
import { postData } from '../utils/apiCall';
import ReduxCredential from './HOC/ReduxCredential';

import RentalStatusSelect from '../_components/RentalStatusSelect';

const NoteModal = ({
  input,
  handleInput,
  handleSelect,
  updateNoteUser,
  isOpen,
  toggle,
  spinner
}) => (
  <Modal isOpen={isOpen} toggle={toggle}>
    {console.log(input)}
    <ModalHeader toggle={toggle}>Note</ModalHeader>
    <ModalBody>
      <FormGroup row>
        <Label sm={2}>Status:</Label>
        <Col sm={10}>
          <RentalStatusSelect
            value={input.rental_status || {}}
            input={input.rental_status || {}}
            onChange={handleSelect('rental_status')}
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Location:</Label>
        <Col sm={10}>
          <Input
            type="text"
            value={input.location_detail || ''}
            onChange={handleInput('location_detail')}
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Price:</Label>
        <Col sm={10}>
          <Input type="text" value={input.price || ''} onChange={handleInput('price')} />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Move in Date:</Label>
        <Col sm={10}>
          <Input
            type="date"
            value={input.move_in_date || ''}
            onChange={handleInput('move_in_date')}
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Note:</Label>
        <Col sm={10}>
          <Input
            type="textarea"
            rows={10}
            value={input.note || ''}
            onChange={handleInput('note')}
          />
        </Col>
      </FormGroup>
    </ModalBody>
    <ModalFooter>
      {spinner ? (
        <Spinner />
      ) : (
        <Button color="primary" onClick={updateNoteUser}>
          Submit
        </Button>
      )}
    </ModalFooter>
  </Modal>
);

export default compose(
  ReduxCredential,
  ControlSpinner,
  withHandlers({
    updateNoteUser: ({
      input,
      updateCard,
      credential: { userId },
      cacheUserId,
      index,
      handleSpinner,
      toggle,
      response,
      setInput
    }) => () => {
      const {
        rental_status, location_detail, price, move_in_date, note
      } = input;

      // if (!status && !followUp) {
      //   // toast.warn('Please fill in information!!');
      //   if (status.value === 3 && !dealValue) {
      //     toast.warn('Please fill in deal value!!');
      //     return;
      //   }
      // }

      // if (status.value === 1 || status.value === 2) {
      //   if (!schedule) {
      //     toast.warn('Please fill in schedule!!');
      //     return;
      //   }
      // }

      handleSpinner();
      postData({
        route: '/cache_users/updateNoteUser',
        data: {
          userId: cacheUserId,
          data: input
        }
      })
        .then(data => {
          debugger;
          response[0].data[index].rental_status = data.data.rental_status;
          response[0].data[index].location_detail = data.data.location_detail;
          response[0].data[index].price = data.data.price;
          response[0].data[index].move_in_date = data.data.move_in_date;
          response[0].data[index].note = data.data.note;
          updateCard({
            loading: false,
            error: false,
            response
          });
          toast.success('Succesfully!!');
          // setInput({});
        })
        .catch(({ message }) => toast.error(message))
        .finally(() => {
          toggle();
          handleSpinner();
        });
    }
  })
)(NoteModal);



