export const getDealNote = followUp => {
  if (followUp.length > 0) {
    return followUp.slice(-1)[0].note;
  }
  return '';
};

export const getListingValue = listing => {
  if (listing) {
    return listing.label;
  }
  return '';
};

export const getDealDate = dealDate => {
  if (dealDate) {
    return dealDate;
  }
  return '';
};

export const getDeal = dealDate => {
  if (dealDate) {
    return dealDate;
  }
  return '';
};



