import React from 'react';
import PropTypes from 'prop-types';
import {
  Row, Button, Table, Col,
  Form, Input, FormGroup
} from 'reactstrap';
import { PAGE_NUMBER } from '../../config';
import { getData } from '../../utils/apiCall';
import { getDealNote } from '../../utils/lead';
import { pathOr } from 'ramda';
import dayjs from 'dayjs';

import { compose, withState, withHandlers } from 'recompose';
import { ChatFeed, Message } from 'react-chat-ui'
import RoomSearch from './Support/RoomSearch'

const DemandSupport = ({
    messages
  }) => {
  return (
    <Row className="animated fadeIn bg-white p-3 pt-4 rounded"
         style={{height:'80vh'}}
    >
      <Col md="3">
        todo: List of current conversations
      </Col>
      <Col md="4" className="border-left border-right border-gray-200">
        <ChatFeed
          messages={messages} // Boolean: list of message objects
          isTyping={false} // Boolean: is the recipient typing
          hasInputField={false} // Boolean: use our input, or use your own
          showSenderName // show the name of the user who sent the message
          bubblesCentered={false} //Boolean should the bubbles be centered in the feed?
          // JSON: Custom bubble styles

        />
        <div>
          <Form>
            <FormGroup>

              <Input
                type="textarea"
                placeholder="Type something..."
                onKeyUp={(event)=>{
                  if(event.which == 13 && !event.shiftKey) {
                    //todo: submit form here
                    event.preventDefault();
                    return false;
                  }
                }}
                style={{
                  width: '100%',
                  height: 60,
                  border: 'none',
                  marginTop: 20,
                  padding: 10
                }}/>
            </FormGroup>
          </Form>
        </div>
      </Col>
      <Col md="5">
        <RoomSearch/>
      </Col>
    </Row>
  );
};

DemandSupport.propTypes = {
  //data: PropTypes.object.isRequired,
  //assignDealer: PropTypes.func.isRequired,
  //setData: PropTypes.func.isRequired
  history: PropTypes.object.isRequired
};
export default compose(
  withState('messages','setMessages',
    [
      new Message({
        id: 1,
        message: "I'm the recipient! (The person you're talking to)",
        senderName: 'Evan',
      }), // Gray bubble
      new Message({
        id: 1,
        message: "I'm the recipient! (The person you're talking to)",
        senderName: 'Evan',
      }), // Gray bubble
      new Message({ id: 0, message: "I'm you -- the blue bubble!" }), // Blue bubble
    ]
    //...
  )
)(DemandSupport);






