import React from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Button,
  Form,
  FormGroup,
  Label,
  Input
} from 'reactstrap';
import { compose, withHandlers, withState, mapProps, lifecycle } from 'recompose';
import ReactHighstock from 'react-highcharts/ReactHighstock.src';
import ReactHighcharts from 'react-highcharts';
import axios from 'axios';
import _ from 'lodash';
const loadjs = require('loadjs');
import {
  Stitch,
  RemoteMongoClient,
  AnonymousCredential
} from "mongodb-stitch-browser-sdk";

const DataPlayground = ({chartConfig}) => (
  <Row className="animated fadeIn">
    <Col>
    <Card>
      <CardHeader>
        <h1>Welcome to Data Console</h1>
      </CardHeader>
      <CardBody>
        <h2>Use the function "chart" to visualize your data</h2>
        <div id="chart-container">
          {
            chartConfig.stockChart ?
              <ReactHighstock config={chartConfig} /> :
              <ReactHighcharts config={chartConfig} />
          }
        </div>
      </CardBody>

    </Card>
    </Col>
  </Row>
);


function makeConsoleAccess(varName, variable) {
  window[varName] = variable;
  console.log('[Bayleaf Playground]',`${varName} is now available!`);
}


export default compose(
  withState('chartConfig','setChartConfig', {}),
  lifecycle({
    componentWillMount() {

      makeConsoleAccess('_',_);
      makeConsoleAccess('axios',axios);
      //makeConsoleAccess('Dexie',Dexie);

      (function() {
        makeConsoleAccess('loadJS',function(jsFile) {
          console.log(jsFile,'is being loaded...');
          loadjs(jsFile).ready(() => {
            console.log(jsFile,'loaded successfully.');
          });
        });
      })();

      makeConsoleAccess('chart', (config) => {
        config = {
          title: {
            text: 'Example chart'
          },
          zoomType: 'xy',
          panning: true,
          panKey: 'meta',
          series: [],
          ...this.props.chartConfig,
          ...config
        };

        this.props.setChartConfig(config);
      });

      makeConsoleAccess('downloadFile', (filename, text) => {
          var element = document.createElement('a');
          element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
          element.setAttribute('download', filename);

          element.style.display = 'none';
          document.body.appendChild(element);

          element.click();
          document.body.removeChild(element);
      });

      (() => {
        const client = Stitch.initializeDefaultAppClient('bayleafprod-ulrwz');
        const db = client.getServiceClient(RemoteMongoClient.factory, 'mongodb-atlas').db('bayleaf');

        console.log("[MongoDB Stitch] Connecting to Stitch...");
        client.auth.loginWithCredential(new AnonymousCredential()).then(() => {
          console.log("[MongoDB Stitch] Connected to Stitch");
          makeConsoleAccess('bayleafDb',db);
        }).catch(err => {
          console.error(err)
        });

      })();
    },

    componentDidMount() {
      window.chart({});
    }
  })
)
(DataPlayground);


