import { compose, mapProps, withHandlers } from 'recompose';
import Joi from 'joi-browser';
import toast from '../../utils/toast';
import { postData } from '../../utils/apiCall';


export default withHandlers({
  submitForm: ({ input, setValidation, handleSpinner, setInput }) =>
    ({validationSchema, apiPath, apiFunction, submitData, handleSuccess, handleError}) =>   {

    if (apiPath) {

      if(!apiFunction) {
        apiFunction = postData;
      }

      submitData = function (data) {
        return apiFunction({ route: apiPath, data: data });
      }
    }
    else if (!submitData) {
      throw new Error('Submit handler is required');
    }

    const { error } = Joi.validate(input, validationSchema, {abortEarly: false, allowUnknown: true});
    if (error) {
      const validation = {};
      error.details.map(({ message, context: { key } }) => {
        validation[key] = message;
      });
      return setValidation(validation);
    }

    handleSpinner();
    submitData(input)
      .then((data) => {
        setValidation({});
        handleSuccess(data);
      })
      .catch(({response:{data:{error:{message}}}})=>{
        let errorObj = new Error(message);
        if(handleError) {
          handleError(errorObj);
        }
        else {
          throw errorObj
        }
      })
      .finally(handleSpinner);
  }
})



