import React from 'react';
import PropTypes from 'prop-types';
import { Row, Table, Col } from 'reactstrap';
import { pathOr } from 'ramda';
import dayjs from 'dayjs';

import { getData } from '../../utils/apiCall';
import createPaginate from '../HOC/createPaginate';
import Search from '../HOC/createSearch';
import { PAGE_NUMBER } from '../../config';

import { getDealNote, getDealDate } from '../../utils/lead';

const Pagination = createPaginate({
  apiCalls: (page, userId, whereQuery) =>
    Promise.all([
      getData({
        route: '/crm_leads',
        query: {
          filter: {
            include: 'dealer',
            order: 'createAt ASC',
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER,
            where: whereQuery
          }
        }
      }),
      getData({
        route: '/crm_leads/count',
        query: { where: whereQuery }
      })
    ])
});

const Lead = ({ data: { response, page }, setData }) => {
  const [
    leads,
    {
      data: { count }
    }
  ] = response;
  return (
    <Row className="animated fadeIn">
      <Col md="12">
        <Pagination setData={setData} count={count} isRoom={false} />
        <Table striped>
          <thead>
            <tr>
              <th>#</th>
              <th>Landlord ID</th>
              <th>Landlord name</th>
              <th>Phone number</th>
              <th>District</th>
              <th>Source</th>
              {/* <th>Hunter</th> */}
              <th>Deal status</th>
              <th>Deal Note</th>
              <th>Deal date</th>
              <th>Dealer</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {leads.data.map((lead, index) => (
              // <LeadCard key={lead.id} input={lead} />
              <tr key={lead.id}>
                <th scope="row">{index + 1 + page * PAGE_NUMBER}</th>
                <td>
                  <a target="_blank" href={lead.profileLink}>
                    {lead.id.substr(0, 5)}
                  </a>
                </td>
                <td>
                  <a target="_blank" href={lead.postLink}>
                    {lead.name}
                  </a>
                </td>
                <td>{lead.rawPhone}</td>
                <td>{lead.district}</td>
                <td>{lead.source}</td>
                {/* <td>Phưn</td> */}
                <td>{lead.status.label}</td>
                <td>{getDealNote(lead.followUp)}</td>
                {/* <td>{dayjs(pathOr(null, ['dealDate'], lead)).format('DD/MM/YYYY')}</td> */}
                <td>{lead.dealDate ? dayjs(lead.dealDate).format('DD/MM/YYYY') : ''}</td>
                <td>{pathOr('', ['dealer', 'username'], lead)}</td>
                <td>
                  <a href={`#/master/edit_lead/${lead.id}`}>
                    Edit <i className="icon-pencil" />
                  </a>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
        {/* <Pagination setData={setData} count={count} whereQuery={whereQuery} /> */}
      </Col>
    </Row>
  );
};

Lead.propTypes = {
  data: PropTypes.object.isRequired,
  setData: PropTypes.func.isRequired
};
export default Lead;



