import React from 'react';
import PropTypes from 'prop-types';
import ControlForm from '../../_components/HOC/ControlForm';

import {
  Row,
  Col,
  Form,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Label,
  Input,
  CardFooter,
  Button
} from 'reactstrap';
import { compose, withHandlers } from 'recompose';

const Search = ({
  input, handleInput, handleSubmit, searchQuery, setSearchQuery, isRoom
}) => (
  <Row>
    <Col md="12">
      <Form onSubmit={handleSubmit}>
        <Card>
          <CardHeader>
            <h3>Search</h3>
          </CardHeader>
          <CardBody>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                {!isRoom ? (
                  <FormGroup>
                    <Label>Search: </Label>
                    <Input
                      type="text"
                      name="district"
                      placeholder="Enter District or Phone or Name Or Post Info"
                      value={input.district || ''}
                      onChange={handleInput('district')}
                    />
                  </FormGroup>
                ) : (
                  <FormGroup>
                    <Label>Search: </Label>
                    <Input
                      type="text"
                      name="phone_number"
                      placeholder="Enter Phone or Room Name or Address or Note"
                      value={input.phone_number || ''}
                      onChange={handleInput('phone_number')}
                    />
                  </FormGroup>
                )}
              </Col>
            </Row>
          </CardBody>
          <CardFooter>
            <Button type="submit" size="md" className="pull-left" color="primary">
              <i className="fa fa-dot-circle-o" /> Search
            </Button>
          </CardFooter>
        </Card>
      </Form>
    </Col>
  </Row>
);

// Search.propTypes = {
//   page: PropTypes.number.isRequired,
//   count: PropTypes.number.isRequired,
//   next: PropTypes.func.isRequired,
//   prev: PropTypes.func.isRequired
// };

export default compose(
  ControlForm,
  withHandlers({
    handleSubmit: ({
      setData, refresh, setWhereQuery, input, isRoom
    }) => e => {
      e.preventDefault();
      if (!isRoom) {
        if (input.district && input.district !== '') {
          setWhereQuery(
            {
              $or: [
                { district: { $regex: `.*${input.district}.*`, $options: 'i' } },
                { rawPhone: { $regex: `.*${input.district}.*`, $options: 'i' } },
                { name: { $regex: `.*${input.district}.*`, $options: 'i' } },
                { postInfo: { $regex: `.*${input.district}.*`, $options: 'i' } }
              ]
            },
            () => {
              refresh();
            }
          );
        } else {
          setWhereQuery({}, () => {
            refresh();
          });
        }
      }

      if (isRoom) {
        if (input.phone_number && input.phone_number !== '') {
          setWhereQuery(
            {
              $or: [
                { phone_number: { $regex: `.*${input.phone_number}.*`, $options: 'i' } },
                { exact_room_address: { $regex: `.*${input.phone_number}.*`, $options: 'i' } },
                { notes: { $regex: `.*${input.phone_number}.*`, $options: 'i' } },
                { room_name: { $regex: `.*${input.phone_number}.*`, $options: 'i' } }
              ]
            },
            () => {
              refresh();
            }
          );
        } else {
          setWhereQuery({}, () => {
            refresh();
          });
        }
      }
    }
  })
)(Search);



