import React from 'react';
import PropTypes from 'prop-types';
import { Row, Button, Table, Col } from 'reactstrap';

import { getData } from '../../utils/apiCall';
import createPaginate from '../HOC/createPaginate';
import { PAGE_NUMBER } from '../../config';

import { getDealNote, getListingValue } from '../../utils/lead';

import { pathOr } from 'ramda';

import dayjs from 'dayjs';
import FollowUpModal from './../FollowUpModal';

const Pagination = createPaginate({
  apiCalls(page, userId, whereQuery) {
    return Promise.all([
      getData({
        route: '/crm_leads',
        query: {
          filter: {
            include: 'dealer',
            where: { 'status.value': { in: [3, 5] }, ...whereQuery },
            order: 'updateAt DESC',
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER
          }
        }
      }),
      getData({
        route: '/crm_leads/count',
        query: {
          where: { 'status.value': { in: [3, 5] }, ...whereQuery }
        }
      })
    ]);
  }
});

const ConvertedList = ({
  data: { response }, setData, releaseDealer, ...props, showPopup, toggleModal, isModalOpen, modalFollow
}) => {
  const [
    leads,
    {
      data: { count }
    }
  ] = response;
  return (
    <Row className="animated fadeIn">
      <Col md="12">
        <Pagination setData={setData} count={count} {...props} isRoom={false}/>
        {/* {leads.data.map((lead, index) => (
        <LeadCard key={lead.id} input={lead} canEditInCard />
      ))} */}
        <Table striped>
          <thead>
            <tr>
              <th>#</th>
              <th>Landlord ID</th>
              <th>Landlord name</th>
              <th>Phone number</th>
              <th>District</th>              
              <th>Source</th>
              {/* <th>Hunter</th> */}
              <th colSpan="2">Deal status</th>
              <th>Deal Note</th>
              <th colSpan="2">Listing</th>
              <th>Deal date</th>
              <th>Deal Value</th>
              <th>Dealer</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {leads.data.map((lead, index) => (
              // <LeadCard key={lead.id} input={lead} />
              <tr key={lead.id}>
                <th scope="row">{index + 1}</th>
                <td>
                  <a target="_blank" href={lead.profileLink}>
                    {lead.id.substr(0, 5)}
                  </a>
                </td>
                <td>
                  <a target="_blank" href={lead.postLink}>
                    {lead.name}
                  </a>
                </td>
                <td>{lead.rawPhone}</td>
                <td>{lead.district}</td>
                <td>{lead.source}</td>
                {/* <td>Phưn</td> */}
                <td>{lead.status.label}</td>
                <td>
                  <Button size="sm" color="link" onClick={() => {
                      showPopup({ leadId: lead.id, index, kindModal:0 })
                    }}>
                    Add <i className="icon-plus" />
                  </Button>
                </td>
                <td>{getDealNote(lead.followUp)}</td>
                <td>{ getListingValue(lead.listing) ==="Yes" ? 
                  <a target="_blank" href={lead.listing.url}>
                    Yes
                  </a>: "No" } </td>
                <td>
                  <Button size="sm" color="link" onClick={() => {
                      showPopup({ leadId: lead.id, index, kindModal:1 })
                    }}>
                    Update <i className="icon-pencil" />
                  </Button>
                </td>
                {/* <td>{dayjs(pathOr(null, ['dealDate'], lead)).format('DD/MM/YYYY')}</td> */}
                <td>{lead.dealDate ? dayjs(lead.dealDate.value).format('DD/MM/YYYY') : ''}</td>
                <td>{pathOr('', ['dealValue', 'label'], lead)}</td>
                <td>{pathOr('', ['dealer', 'username'], lead)}</td>
                <td>
                  <a href={`#/master/edit_lead/${lead.id}`}>
                    Edit <i className="icon-pencil" />
                  </a>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>

        <FollowUpModal
          kindModal={modalFollow.kindModal}
          isOpen={isModalOpen}
          toggle={()=>toggleModal(!isModalOpen)}
          leadId={modalFollow.leadId}
          index={modalFollow.currentIndex}
          updateCard={setData}
          response={response}
        />
      </Col>
    </Row>
  );
};

ConvertedList.propTypes = {
  data: PropTypes.object.isRequired,
  releaseDealer: PropTypes.func.isRequired
};
export default ConvertedList;



