const charsWithDiacritics='ÀÁÂÃÈÉÊÌÍÒÓÔÕÙÚÝàáâãèéêìíòóôõùúýĂăĐđĨĩŨũƠơƯưẠạẢảẤấẦầẨẩẪẫẬậẮắẰằẲẳẴẵẶặẸẹẺẻẼẽẾếỀềỂểỄễỆệỈỉỊịỌọỎỏỐốỒồỔổỖỗỘộỚớỜờỞởỠỡỢợỤụỦủỨứỪừỬửỮữỰựỲỳỴỵỶỷỸỹ';
const charsNormalized = 'AAAAEEEIIOOOOUUYaaaaeeeiioooouuyAaDdIiUuOoUuAaAaAaAaAaAaAaAaAaAaAaAaEeEeEeEeEeEeEeEeIiIiOoOoOoOoOoOoOoOoOoOoOoOoUuUuUuUuUuUuUuYyYyYyYy';
const charsWithDiacriticsPositionMap = {};

charsWithDiacritics.split('').forEach(function(charWithDiacritics, position) {
  charsWithDiacriticsPositionMap[charWithDiacritics] = position;
});

function removeDiacriticsFromChar(anyChar) {
  var charPosition = charsWithDiacriticsPositionMap[anyChar];
  if(charPosition !== undefined) {
    return charsNormalized[charPosition];
  }
  else {
    if(anyChar.match(/[̣̉̀́̃]/)) {
      return '';
    }
    else {
      return anyChar;
    }
  }
}

export default (stringWithDiacritics = '') => (
  stringWithDiacritics
    .split('')
    .map(
      charWithDiacritics => (
        removeDiacriticsFromChar(charWithDiacritics)
      )
    )
    .join('')
)



