import React from 'react';
import Select from 'react-select';
import { compose } from 'recompose';

import { declinedReason } from '../config';
import ReduxCredential from '../_components/HOC/ReduxCredential';
import ControlSpinner from '../_components/HOC/ControlSpinner';

const DeclinedReasonSelect = ({ value, onChange, isDisabled }) => (
  <Select value={value} onChange={onChange} options={declinedReason} isDisabled={isDisabled} />
);

export default compose(
  ReduxCredential,
  ControlSpinner
)(DeclinedReasonSelect);



