import React from 'react';
import Select from 'react-select';
import { compose } from 'recompose';

import { leadStatus } from '../config';
import ReduxCredential from '../_components/HOC/ReduxCredential';
import ControlSpinner from '../_components/HOC/ControlSpinner';

const StatusSelect = ({ input, onChange }) => (
  <Select value={input.status} onChange={onChange} options={leadStatus} />
);

export default compose(
  ReduxCredential,
  ControlSpinner
)(StatusSelect);



