import React from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Table,
  Pagination,
  PaginationItem,
  PaginationLink,
  Button,
  Progress,
  Form,
  FormGroup,
  Label,
  Input,
  Badge,
  ButtonDropdown,
  DropdownToggle,
  DropdownItem,
  DropdownMenu
} from 'reactstrap';
import {
  compose,
  withHandlers,
  withState,
  withProps,
  mapProps,
  lifecycle,
  withStateHandlers
} from 'recompose';
import { Typeahead } from 'react-bootstrap-typeahead';
import ControlForm from '../../_components/HOC/ControlForm';
import ControlAnalyticsForm from '../../_components/HOC/ControlAnalyticsForm';
import ChartSeriesControl from '../../_components/Button/Analytics/ChartSeriesControl';
import ControlValidation from '../../_components/HOC/ControlValidation';
import ControlSpinner from '../../_components/HOC/ControlSpinner';
import { Spinner } from '../../_components/common';
import FetchData from '../../_components/HOC/FetchData';
import { getData } from '../../utils/apiCall';
import { map, find, remove } from 'lodash';

import ReactHighstock from 'react-highcharts/ReactHighstock.src'; // Expects that Highcharts was loaded in the code.

const EventBrowser = ({
  handleInput,
  getNumOfDaysBetweenFromDateAndToDate,
  history,
  handleSubmit,
  input,
  chartConfig,
  eventTypes,
  handleAppEventTypeSelected,
  hideEvenType,
  showEventType,
  removeEventType,
  chartTickIntervalOptions,
  handleSelectTickInterval,
  spinner,
  eventDownloadProgress,
  handleSaveInputToUrl
}) => (
  <Row className="animated fadeIn">
    <Col>
      <Card>
        <CardHeader>
          <i className="fa fa-line-chart" />
          <Form>
            <FormGroup>
              <Label for="exampleDatetime">From Date</Label>
              <Input
                type="date"
                placeholder="date placeholder"
                value={input.fromDate}
                onChange={handleInput('fromDate')}
              />
            </FormGroup>
            <FormGroup>
              <Label for="exampleDatetime">To Date</Label>
              <Input
                type="date"
                placeholder="date placeholder"
                value={input.toDate}
                onChange={handleInput('toDate')}
              />
            </FormGroup>
            <FormGroup>
              <Input plaintext>
                Displaying events of {getNumOfDaysBetweenFromDateAndToDate()} days
              </Input>
            </FormGroup>
            <FormGroup>
              <Label for="exampleSelect">Interval</Label>
              <Input
                type="select"
                value={input.chartTickInterval}
                onChange={handleSelectTickInterval}
              >
                <option value="">Select an Interval</option>
                {map(chartTickIntervalOptions, ({ label: tickIntervalLabel }, tickIntervalCode) => (
                  <option value={tickIntervalCode}>{tickIntervalLabel}</option>
                ))}
              </Input>
            </FormGroup>
            <FormGroup>
              <Label for="exampleDatetime">Choose Events to Add</Label>
              <Typeahead
                labelKey="code"
                onChange={handleAppEventTypeSelected}
                options={eventTypes}
              />
            </FormGroup>
            <FormGroup>
              {input.addedAppEventTypes &&
                input.addedAppEventTypes.map(addedAppEventType => (
                  <ChartSeriesControl
                    key={`event-type-control--${addedAppEventType.code}`}
                    label={addedAppEventType.code}
                    onRemove={removeEventType(addedAppEventType)}
                  />
                ))}
            </FormGroup>
            {
              // <div>
              //  {JSON.stringify(input)}
              // </div>
            }

            {
              <FormGroup>
                <Button
                  className="pull-left mr-2"
                  type="submit"
                  size="lg"
                  color="primary"
                  onClick={handleSaveInputToUrl}
                >
                  <strong>Save My Work</strong>
                </Button>

                {spinner ? (
                  <React.Fragment>
                    <div className="pull-left">
                      <Spinner />
                    </div>
                  </React.Fragment>
                ) : (
                  <React.Fragment>
                    <Button
                      className="pull-left"
                      type="submit"
                      size="lg"
                      color="primary"
                      onClick={handleSubmit}
                    >
                      <strong>Save and Load Events Now!</strong>
                    </Button>
                  </React.Fragment>
                )}
              </FormGroup>
            }
          </Form>
        </CardHeader>
        <Progress multi>
          <Progress style="height:10px" bar value={eventDownloadProgress} />
        </Progress>
        <CardBody>
          <ReactHighstock config={chartConfig} />
        </CardBody>
      </Card>
    </Col>
  </Row>
);

EventBrowser.propTypes = {
  // data: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
};
export default compose(
  FetchData(() => getData({ route: '/app_events/types' })),
  mapProps(({ data: { response: { data: eventTypes } }, ...otherProps }) => ({
    eventTypes,
    ...otherProps
  })),
  ControlValidation,
  ControlForm,
  ControlSpinner,
  ControlAnalyticsForm,
  withState('eventDownloadProgress', 'setEventDownloadProgress', 0),
  withHandlers({
    fetchWeatherData: ({
      input,
      translateFromDateToTimeStamp,
      translateToDateToTimeStamp,
      chartConfig,
      setChartConfig
    }) => () =>
      /**
       * https://openweathermap.org/api
       * var appid = 'b1b15e88fa797225412429c1c50c122a1';
       * var hoChiMinhCityID = '1566083';
       * http://history.openweathermap.org/data/2.5/history/city
       */

      getData({
        route: '/weather/history',
        params: {
          cityCode: 'hcm',
          startTime: translateFromDateToTimeStamp(input.fromDate),
          endTime: translateToDateToTimeStamp(input.toDate)
        }
      })
  }),
  withHandlers({
    handleAppEventTypeSelected: ({ input, setInput }) => selectedAppEventTypes => {
      if (!input.addedAppEventTypes) {
        input.addedAppEventTypes = [];
      }
      selectedAppEventTypes.forEach(selectedAppEventType => {
        if (
          !input.addedAppEventTypes.find(addedAppEventType => addedAppEventType.code == selectedAppEventType.code)
        ) {
          input.addedAppEventTypes.push(selectedAppEventType);
        }
      });

      setInput({ ...input });
    },

    removeEventType: ({ input, setInput }) => eventType => () => {
      remove(input.addedAppEventTypes, { code: eventType.code });
      setInput({ ...input });
    },

    handleSubmit: ({
      history,
      input,
      chartConfig,
      setChartConfig,
      handleSpinner,
      setEventDownloadProgress,
      handleSaveInputToUrl,
      translateFromDateToTimeStamp,
      translateToDateToTimeStamp,
      fetchWeatherData
    }) => () => {
      handleSaveInputToUrl();

      let eventFilter = {
        where: {
          and: [
            {
              timestamp: {
                between: [
                  translateFromDateToTimeStamp(input.fromDate),
                  translateToDateToTimeStamp(input.toDate)
                ]
              }
            }
          ],
          type: { inq: map(input.addedAppEventTypes, ({ code }) => code) }
        },
        fields: {
          type: true,
          timestamp: true
        }
      };

      handleSpinner();
      setEventDownloadProgress(0);
      Promise.all([
        fetchWeatherData(),
        getData({
          route: '/app_events/',
          query: {
            filter: eventFilter
          },
          onDownloadProgress: progressEvent => {
            setEventDownloadProgress((progressEvent.loaded * 100) / progressEvent.total);
          }
        })
      ])
        .then(([{ data: hourlyWeatherData }, { data: events }]) => {
          console.log(hourlyWeatherData);
          let humiditySeries = find(chartConfig.series, { id: 'weather-humidity' });
          if (!humiditySeries) {
            humiditySeries = {
              id: 'weather-humidity',
              yAxis: 'humidity',
              name: 'Humidity',
              dataGrouping: {
                approximation: 'average'
              },
              data: []
            };
            chartConfig.series.push(humiditySeries);
          }

          humiditySeries.data = map(hourlyWeatherData, ({ time, humidity }) => [time, humidity]);

          let tempSeries = find(chartConfig.series, { id: 'weather-temp' });
          if (!tempSeries) {
            tempSeries = {
              id: 'weather-temp',
              yAxis: 'temperature',
              name: 'Temperature',
              dataGrouping: {
                approximation: 'average'
              },
              data: []
            };
            chartConfig.series.push(tempSeries);
          }

          tempSeries.data = map(hourlyWeatherData, ({ time, temp }) => [time, temp]);

          let rainSeries = find(chartConfig.series, { id: 'weather-rain' });
          if (!rainSeries) {
            rainSeries = {
              id: 'weather-rain',
              yAxis: 'rain',
              name: 'Rain',
              dataGrouping: {
                approximation: 'sum'
              },
              data: []
            };
            chartConfig.series.push(rainSeries);
          }

          rainSeries.data = map(hourlyWeatherData, ({ time, rain }) => [time, rain ? 200 : 0]);

          remove(chartConfig.series, series => series.id && series.id.match('app-event--'));

          let eventTypeToEventSeriesMap = {};

          events.forEach(event => {
            let eventSeriesOfSameType = eventTypeToEventSeriesMap[event.type];
            if (!eventSeriesOfSameType) {
              eventSeriesOfSameType = [];
            }
            eventSeriesOfSameType.push([event.timestamp, 1]);
            eventTypeToEventSeriesMap[event.type] = eventSeriesOfSameType;
          });

          chartConfig.series.push.apply(
            chartConfig.series,
            map(eventTypeToEventSeriesMap, (eventSeries, eventType) => ({
              name: eventType,
              id: `app-event--${eventType}`,
              data: eventSeries
            }))
          );

          /** Debug * */
          console.log(chartConfig);
          window.setChartConfig = setChartConfig;
          window.chartConfig = chartConfig;

          setChartConfig({ ...chartConfig });
        })
        .finally(handleSpinner);
    },
    getNumOfDaysBetweenFromDateAndToDate: ({ input }) => () => {
      if (input.fromDate && input.toDate) {
        return daysBetween(input.fromDate, input.toDate);
      }

      return 0;
    }
  }),
  lifecycle({
    componentWillMount() {
      this.props.setChartConfig(
        {
          time: { timezoneOffset: new Date().getTimezoneOffset() },
          legend: { enabled: true },
          rangeSelector: {
            selected: 1
          },
          title: {
            text: 'Events in Selected Range'
          },
          xAxis: {
            ordinal: false
          },
          yAxis: [{}, { id: 'rain' }, { id: 'temperature' }, { id: 'humidity' }],
          plotOptions: {
            series: {
              dataGrouping: {
                approximation: 'sum',
                forced: true,
                groupAll: true,
                units: [['hour', [1]]]
              }
            }
          },
          series: []
        },
        () => {
          this.props.handleFillInputFromUrl();
          setTimeout(this.props.handleSubmit);
        }
      );
    }
  })
)(EventBrowser);

const daysBetween = (() => {
  const one_day = 1000 * 60 * 60 * 24;
  return (date1, date2) => {
    // Get 1 day in milliseconds

    // Convert both dates to milliseconds
    let date1_ms = new Date(date1).getTime();
    let date2_ms = new Date(date2).getTime();

    // Calculate the difference in milliseconds
    let difference_ms = date2_ms - date1_ms;

    // Convert back to days and return
    return Math.round(difference_ms / one_day);
  };
})();



