import React from 'react';
import PropTypes from 'prop-types';
import { Row, Button, Table, Col } from 'reactstrap';

import { getData } from '../../utils/apiCall';
import createPaginate from '../HOC/createPaginate';
import { PAGE_NUMBER } from '../../config';

import RoomNoteModal from './../RoomNoteModal';
import { pathOr } from 'ramda';
import dayjs from 'dayjs';

const Pagination = createPaginate({
  apiCalls(page, userId, whereQuery) {
    return Promise.all([
      getData({
        route: '/rooms/',
        query: {
          filter: {
            order: 'updated_date DESC',
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER,
            where: { is_featured_room: true, ...whereQuery }
          }
        }
      }),
      getData({
        route: '/rooms/count',
        query: {
          where: { is_featured_room: true, ...whereQuery }
        }
      })
    ]);
  }
});

const HotRoom = ({
  data: { response },
  setData,
  addHotRoom,
  removeHotRoom,
  toggleModal,
  isModalOpen,
  modalNote,
  input,
  setInput,
  handleInput,
  handleSelect,
  setModalNote,
  ...props
}) => {
  const [
    leads,
    {
      data: { count }
    }
  ] = response;
  return (
    <Row className="animated fadeIn">
      <Col md="12">
        <Pagination setData={setData} count={count} {...props} isRoom />
        <Table striped>
          <thead>
            <tr>
              <th>#</th>
              <th>Room ID</th>
              <th>Room name</th>
              <th>Phone</th>
              <th>Address</th>
              <th>Price</th>
              <th>Note</th>
              <th>Status</th>
              <th>Date</th>
              <th>Hot Room Action</th>
            </tr>
          </thead>
          <tbody>
            {leads.data.map((lead, index) => (
              // <LeadCard key={lead.id} input={lead} />
              <tr key={lead.id}>
                <th scope="row">{index + 1}</th>
                <td>{lead.id}</td>
                <td>{lead.room_name}</td>
                <td>{lead.phone_number}</td>
                <td>{lead.exact_room_address}</td>
                <td>{lead.room_price}</td>
                <td>{lead.note}</td>
                <td>{pathOr('', ['available_status', 'label'], lead)}</td>
                <td>
                  {lead.available_status_date
                    ? dayjs(lead.available_status_date).format('DD/MM/YYYY HH:mm')
                    : ''}
                </td>
                <td>
                  {!lead.is_hot_room ? (
                    <Button
                      color="link"
                      size="sm"
                      onClick={() => {
                        addHotRoom({ roomId: lead.id, index });
                      }}
                    >
                      Add <i className="fa fa-plus-square" />
                    </Button>
                  ) : (
                    <Button
                      color="link"
                      size="sm"
                      onClick={() => {
                        removeHotRoom({ roomId: lead.id, index });
                      }}
                    >
                      Remove <i className="fa fa-minus" />
                    </Button>
                  )}

                  <Button
                    size="sm"
                    color="link"
                    onClick={() => {
                      setModalNote({ roomId: lead.id, currentIndex: index });
                      setInput(
                        {
                          note: lead.note,
                          available_status: lead.available_status
                        },
                        () => toggleModal(true)
                      );
                    }}
                  >
                    Update <i className="icon-pencil" />
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>

        <RoomNoteModal
          input={input}
          isOpen={isModalOpen}
          toggle={() => toggleModal(!isModalOpen)}
          roomId={modalNote.roomId}
          index={modalNote.currentIndex}
          updateCard={setData}
          response={response}
          handleInput={handleInput}
          handleSelect={handleSelect}
        />
      </Col>
    </Row>
  );
};

HotRoom.propTypes = {
  data: PropTypes.object.isRequired
};
export default HotRoom;



