import React from 'react';
import PropTypes from 'prop-types';
import ControlForm from '../../_components/HOC/ControlForm';

import {
  Row,
  Col,
  Form,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Label,
  Input,
  CardFooter,
  Button
} from 'reactstrap';
import { compose, withHandlers } from 'recompose';

const Search = ({
  input, handleInput, handleSubmit, searchQuery, setSearchQuery
}) => (
  <Row>
    <Col md="12">
      <Form onSubmit={handleSubmit}>
        <Card>
          <CardHeader>
            <h3>Search</h3>
          </CardHeader>
          <CardBody>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Search: </Label>
                  <Input
                    type="text"
                    name="district"
                    placeholder="Enter id or name"
                    value={input.district || ''}
                    onChange={handleInput('district')}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Arrange Date:</Label>
                  <Input
                    type="date"
                    value={input.arrangeDate || ''}
                    onChange={handleInput('arrangeDate')}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Visit Date:</Label>
                  <Input
                    type="date"
                    value={input.visitDate || ''}
                    onChange={handleInput('visitDate')}
                  />
                </FormGroup>
              </Col>
            </Row>
          </CardBody>
          <CardFooter>
            <Button type="submit" size="md" className="pull-left" color="primary">
              <i className="fa fa-dot-circle-o" /> Search
            </Button>
          </CardFooter>
        </Card>
      </Form>
    </Col>
  </Row>
);

export default compose(
  ControlForm,
  withHandlers({
    handleSubmit: ({
      setData, refresh, setWhereQuery, input
    }) => e => {
      e.preventDefault();
      debugger;
      if (
        (input.district && input.district !== '') ||
        (input.arrangeDate && input.arrangeDate !== '') ||
        (input.visitDate && input.visitDate !== '')
      ) {
        let query = {};

        if (input.district && input.district !== '') {
          query.$or = [
            { 'cache_users._id': { $regex: `.*${input.district}.*`, $options: 'i' } },
            { 'cache_users.name': { $regex: `.*${input.district}.*`, $options: 'i' } }
          ];
        }

        if (input.arrangeDate && input.arrangeDate !== '') {
          query.arrangeDate = input.arrangeDate;
        }

        if (input.visitDate && input.visitDate !== '') {
          query.visitDate = input.visitDate;
        }

        setWhereQuery(query, () => {
          refresh();
        });
      } else {
        setWhereQuery({}, () => {
          refresh();
        });
      }
    }
  })
)(Search);



