import { compose, withHandlers } from 'recompose';
import { postData } from '../../utils/apiCall';
import toast from '../../utils/toast';

export default compose(
  withHandlers({
    checkDuplicate: ({input, handleSpinner}) => () => {
      handleSpinner();
      postData({
        route: `/crm_leads/checkDuplicate`,
        data: input.id ?
          {id: input.id, rawPhone: input.rawPhone} :
          {rawPhone: input.rawPhone}
      })
        .then(({data:{hasDuplicates}})=>{
          hasDuplicates ?
            toast.error(`Duplication Detected`) :
            toast.success(`OK. No duplication detected`);
        })
        .catch((error)=>{
          if(error.response) {
            toast.error(error.response.data.error.message);
          }
          else {
            throw error;
          }
        })
        .finally(handleSpinner);
    }
  })
);


