import { compose, withState, withHandlers } from 'recompose';

import FetchData from '../../_components/HOC/FetchData';
import { getData, postData } from '../../utils/apiCall';
import HotRoomList from '../../_components/List/HotRoomList';
import ReduxCredential from '../../_components/HOC/ReduxCredential';
import toast from '../../utils/toast';
import { PAGE_NUMBER } from '../../config';
import ControlModal from '../../_components/HOC/ControlModal';
import ControlForm from '../../_components/HOC/ControlForm';

export default compose(
  withState('page', 'setPage', 0),
  withState('modalNote', 'setModalNote', { roomId: '', currentIndex: 0 }),
  withState('modalFollow', 'setModalFollow', { leadID: '', currentIndex: 0, kindModal: 0 }),
  ReduxCredential,
  ControlModal('isModalOpen', 'toggleModal', false),
  ControlForm,
  FetchData(function () {
    const { userId } = this.props.credential;
    return Promise.all([
      getData({
        route: '/rooms/',
        query: {
          filter: {
            order: 'updated_date DESC',
            limit: PAGE_NUMBER,
            skip: 0,
            where: { is_featured_room: true }
          }
        }
      }),
      getData({
        route: '/rooms/count',
        query: {
          where: { is_featured_room: true }
        }
      })
    ]);
  }),
  withHandlers({
    addHotRoom: ({ credential: { userId }, data, setData }) => ({ roomId, index }) => {
      postData({
        route: '/rooms/addHotRoom',
        data: {
          userId,
          roomId
        }
      })
        .then(res => {
          toast.success('Successfully!!');
          const newData = { ...data };
          res.data.data.id = res.data.data._id;
          newData.response[0].data[index] = res.data.data;
          setData(newData);
        })
        .catch(({ response }) => {
          const { message } = response.data.error;
          toast.error(message);
        });
    },
    removeHotRoom: ({ credential: { userId }, data, setData }) => ({ roomId, index }) => {
      postData({
        route: '/rooms/removeHotRoom',
        data: {
          userId,
          roomId
        }
      })
        .then(res => {
          toast.success('Successfully!!');
          const newData = { ...data }; // after assign then total will be increase 1
          res.data.data.id = res.data.data._id;
          newData.response[0].data[index] = res.data.data;
          setData(newData);
        })
        .catch(({ response }) => {
          const { message } = response.data.error;
          toast.error(message);
        });
    }
  })
)(HotRoomList);



