import { compose, withState, withHandlers } from 'recompose';

import FetchData from '../../_components/HOC/FetchData';
import { getData, postData } from '../../utils/apiCall';
import DealListMe from '../../_components/List/DealListMe';
import ReduxCredential from '../../_components/HOC/ReduxCredential';
import toast from '../../utils/toast';
import { PAGE_NUMBER } from '../../config';
import ControlModal from '../../_components/HOC/ControlModal';

export default compose(
  withState('page', 'setPage', 0),
  withState('modalFollow', 'setModalFollow', { leadID: '', currentIndex: 0, kindModal: 0 }),
  ReduxCredential,
  ControlModal('isModalOpen', 'toggleModal', false),
  FetchData(function () {
    const { userId } = this.props.credential;
    return Promise.all([
      getData({
        route: `/crm_users/${userId}/crmDeal`,
        query: {
          filter: {
            include: 'dealer',
            order: 'schedule DESC, createAt ASC',
            limit: PAGE_NUMBER,
            skip: 0
          }
        }
      }),
      getData({ route: `/crm_users/${userId}/crmDeal/count` })
    ]);
  }),
  withHandlers({
    releaseDealer: ({ credential: { userId }, data, setData }) => ({ leadId, index }) => {
      postData({
        route: '/crm_users/releaseDealer',
        data: {
          userId,
          leadId
        }
      })
        .then(() => {
          toast.warn('Successfully release lead!!');
          // const newData = { ...data };
          // newData.response.data.splice(index, 1);
          const newData = { ...data }; // after assign then total will be increase 1
          newData.response[0].data.splice(index, 1);
          newData.response[1].data.count -= 1;
          setData(newData);
        })
        .catch(({ response }) => {
          const { message } = response.data.error;
          toast.error(message);
        });
    },
    listingDealer: ({ credential: { userId }, data, setData }) => ({ leadId, index }) => {
      postData({
        route: '/crm_leads/listingDealer',
        data: {
          leadId
        }
      })
        .then(() => {
          toast.warn('Successfully release lead!!');
          // const newData = { ...data };
          // newData.response.data.splice(index, 1);
          // const newData = { ...data }; // after assign then total will be increase 1
          // newData.response[0].data.splice(index, 1);
          // newData.response[1].data.count -= 1;
          // setData(newData);
        })
        .catch(({ response }) => {
          const { message } = response.data.error;
          toast.error(message);
        });
    }
  }),
  withHandlers({
    showPopup: ({ toggleModal, setModalFollow }) => ({ leadId, index, kindModal }) => {
      setModalFollow({ leadId, currentIndex: index, kindModal });
      toggleModal(true);
    }
  })
)(DealListMe);



