import axios from 'axios';
import { apiBaseUrl } from '../config';

export const getData = ({ route = '', query = {}, ...config }) =>
  axios({
    method: 'get',
    url: `${apiBaseUrl}${route}`,
    params: query,
    ...config
  });

export const postData = ({ route = '', data = {}, ...config }) =>
  axios({
    method: 'post',
    url: `${apiBaseUrl}${route}`,
    data,
    ...config
  });

export const patchData = ({ route = '', data = {}, ...config }) =>
  axios({
    method: 'patch',
    url: `${apiBaseUrl}${route}`,
    data,
    ...config
  });

export const putData = ({ route = '', data = {}, ...config }) =>
  axios({
    method: 'put',
    url: `${apiBaseUrl}${route}`,
    data,
    ...config
  });

export const deleteData = ({ route = '', data = {}, ...config }) =>
  axios({
    method: 'delete',
    url: `${apiBaseUrl}${route}`,
    data,
    ...config
  });



