import React from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Button,
  Form,
  FormGroup,
  Label,
  Input
} from 'reactstrap';
import { compose, withHandlers, withState, mapProps, lifecycle } from 'recompose';
import ControlForm from '../../_components/HOC/ControlForm';
import ChartSeriesControl from '../../_components/Button/Analytics/ChartSeriesControl';
import ControlAnalyticsForm from '../../_components/HOC/ControlAnalyticsForm';
import ControlValidation from '../../_components/HOC/ControlValidation';
import ControlSpinner from '../../_components/HOC/ControlSpinner';
import { Spinner } from '../../_components/common';
import FetchData from '../../_components/HOC/FetchData';
import { getData } from '../../utils/apiCall';
import { StackingColorResolver } from '../../utils/ChartColorResolvers';
import { map, find, remove, each, values, findLast } from 'lodash';

import ReactHighstock from 'react-highcharts/ReactHighstock.src'; // Expects that Highcharts was loaded in the code.
// Expects that Highcharts was loaded in the code.
const queryString = require('qs');

const SalesAnalytics = ({
  handleInput,
  handleSubmit,
  input,
  chartConfig,
  spinner,
  handleSaveInputToUrl,
  removeAddedConverterUser,
  handleSelectTickInterval,
  chartTickIntervalOptions
}) => (
  <Row className="animated fadeIn">
    <Col>
      <Card>
        <CardHeader>
          <i className="fa fa-line-chart" />
          <Form>
            <FormGroup>
              <Label>From Date</Label>
              <Input
                type="date"
                placeholder="date placeholder"
                value={input.fromDate}
                onChange={handleInput('fromDate')}
              />
            </FormGroup>
            <FormGroup>
              <Label>To Date</Label>
              <Input
                type="date"
                placeholder="date placeholder"
                value={input.toDate}
                onChange={handleInput('toDate')}
              />
            </FormGroup>
            <FormGroup>
              <Label for="exampleSelect">Interval</Label>
              <Input
                type="select"
                value={input.chartTickInterval}
                onChange={handleSelectTickInterval}
              >
                <option value="">Select an Interval</option>
                {map(chartTickIntervalOptions, ({ label: tickIntervalLabel }, tickIntervalCode) => (
                  <option
                    key={`tick-interval-option--${tickIntervalCode}`}
                    value={tickIntervalCode}
                  >
                    {tickIntervalLabel}
                  </option>
                ))}
              </Input>
            </FormGroup>
            {/* <FormGroup>
              <Label>Choose Users to Add</Label>
              <Typeahead
                labelKey="name"
                onChange={handleConverterUsersSelected}
                options={converterUsers}
              />
            </FormGroup> */}

            <FormGroup>
              {input.addedConverterUsers &&
                input.addedConverterUsers.map(addedConverterUser => (
                  <ChartSeriesControl
                    key={`convert-user-controls--${addedConverterUser.id}`}
                    label={addedConverterUser.name}
                    onRemove={removeAddedConverterUser(addedConverterUser)}
                  />
                ))}
            </FormGroup>

            {
              <FormGroup>
                <Button
                  className="pull-left mr-2"
                  type="submit"
                  size="lg"
                  color="primary"
                  onClick={handleSaveInputToUrl}
                >
                  <strong>Save My Work</strong>
                </Button>

                {spinner ? (
                  <React.Fragment>
                    <div className="pull-left">
                      <Spinner />
                    </div>
                  </React.Fragment>
                ) : (
                  <React.Fragment>
                    <Button
                      className="pull-left"
                      type="submit"
                      size="lg"
                      color="primary"
                      onClick={handleSubmit}
                    >
                      <strong>Save and Load Data Now!</strong>
                    </Button>
                  </React.Fragment>
                )}
              </FormGroup>
            }
          </Form>
        </CardHeader>
        {/* <Progress multi>
          <Progress style="height:10px" bar value={eventDownloadProgress} />
        </Progress> */}
        <CardBody>
          <ReactHighstock config={chartConfig} />
        </CardBody>
      </Card>
    </Col>
  </Row>
);

SalesAnalytics.propTypes = {
  // data: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
};

export default compose(
  FetchData(() =>
    getData({
      route: '/crm_users',
      query: {
        filter: { where: { role: 'supply/sales/converter' }, fields: { name: true, id: true } }
      }
    })),
  mapProps(({ data: { response: { data: converterUsers } }, ...otherProps }) => ({
    converterUsers,
    ...otherProps
  })),
  ControlValidation,
  ControlForm,
  ControlSpinner,
  ControlAnalyticsForm,
  withState('eventDownloadProgress', 'setEventDownloadProgress', 0),
  withHandlers({
    handleSaveInputToUrl: ({ history, input }) => () => {
      history.push({
        search: `?${queryString.stringify(input)}`
      });
    }
  }),
  withHandlers({
    handleConverterUsersSelected: ({ input, setInput }) => selectedConverterUsers => {
      if (!input.addedConverterUsers) {
        input.addedConverterUsers = [];
      }
      selectedConverterUsers.forEach(selectedConverterUser => {
        if (
          !input.addedConverterUsers.find(addedAppEventType => addedAppEventType.code == selectedConverterUser.code)
        ) {
          input.addedConverterUsers.push(selectedConverterUser);
        }
      });

      setInput({ ...input });
    },

    removeAddedConverterUser: ({ input, setInput }) => user => () => {
      remove(input.addedConverterUsers, { id: user.id });
      setInput({ ...input });
    },

    handleSubmit: ({
      history,
      input,
      chartConfig,
      setChartConfig,
      handleSpinner,
      setEventDownloadProgress,
      handleSaveInputToUrl,
      translateFromDateToISOString,
      translateToDateToISOString,
      converterUsers
    }) => () => {
      handleSaveInputToUrl();

      // let userSearchFilter = {
      //  where: {
      //    and: [
      //      {
      //        timestamp: {
      //          between: [
      //            translateFromDateToTimeStamp(input.fromDate),
      //            translateToDateToTimeStamp(input.toDate)
      //          ]
      //        }
      //      }
      //    ],
      //    userId: { inq: map(input.addedConverterUsers, ({ id }) => id) }
      //  },
      //  fields: {
      //    //type: true,
      //    //timestamp: true
      //  }
      // };

      /**
       * TODO: fetch sales analytics data here
       */
      handleSpinner();
      setEventDownloadProgress(0);

      Promise.all([
        getData({
          route: '/crm_leads/',
          query: {
            filter: {
              where: {
                createAt: {
                  between: [
                    translateFromDateToISOString(input.fromDate),
                    translateToDateToISOString(input.toDate)
                  ]
                }
              },
              fields: {
                id: true,
                createAt: true,
                dealerId: true,
                source: true,
                district: true
              }
            }
          }
        }),
        getData({
          route: '/crm_leads/contacted',
          query: {
            fromDate: translateFromDateToISOString(input.fromDate),
            toDate: translateToDateToISOString(input.toDate)
          }
        })
      ])
        .then(([{ data: list_leadGenerated }, { data: list_leadContacted }]) => {
          console.log('Leads Generated', list_leadGenerated);
          console.log('Leads Contacted', list_leadContacted);

          // remove(chartConfig.series, series => series.id && series.id.match('leads-generated'));
          chartConfig.series.length = 0;

          let stackingColorResolver = new StackingColorResolver();

          /** === Setting Leadgen's data series === */
          chartConfig.series.push({
            type: 'column',
            name: 'Leads Generated',
            id: 'leads-generated',
            data: list_leadGenerated
              .map(({ createAt }) => [new Date(createAt).getTime(), 1])
              .sort(([time1], [time2]) => time1 - time2),
            dataLabels: {
              enabled: true
            }
          });

          /**
           * Build userId map for fast reference
           */
          let converterUserIdMap = {};
          each(converterUsers, converterUser => {
            converterUserIdMap[converterUser.id] = converterUser;
          });

          let dealerIdToDealerFollowUpSeries = {};
          let dealerIdToDealerConversionSeries = {};

          list_leadContacted.forEach(({ _id, dealerId, followUp: followUpList }) => {
            if (!dealerId) {
              console.error('WTF? no dealerID? at lead: ', _id);
              return;
            }

            /**
             * When multiple "converts" happen continuously on the same lead,
             * it means the first convert IS the actual convert,
             * the subsequent converts are only to update status.
             *
             * Because of this data behavior, we will count a lead's followUps by taking the last group of followUps
             * that have the same status as one representative followUp, and the time of that representative followUp
             * is the time of the first followUp in the group.
             */
            let representativeFollowUp = findLast(
              followUpList,
              (eachFollowUp, eachFollowUpIndex) => {
                let followUpBeforeThisFollowUp = eachFollowUp[eachFollowUpIndex - 1];
                return (
                  !followUpBeforeThisFollowUp ||
                  followUpBeforeThisFollowUp.status.value != eachFollowUp.status.value
                );
              }
            );

            /**
             * Build chart series for followUps
             */
            let dealerFollowUpSeries = dealerIdToDealerFollowUpSeries[dealerId];
            if (converterUserIdMap[dealerId]) {
              if (!dealerFollowUpSeries) {
                let converterUserName = converterUserIdMap[dealerId].name;
                let seriesId = `leads-contacted-by--${dealerId}`;
                let stackId = 'leads-contacted-by-individual';
                dealerFollowUpSeries = {
                  type: 'column',
                  name: `Leads Contacted by "${converterUserName}"`,
                  id: seriesId,
                  stack: stackId,
                  stacking: 'normal',
                  tooltipOptions: {
                    split: false
                  },
                  dataLabels: {
                    enabled: true,
                    formatter() {
                      return this.point.percentage
                        ? `${this.point.y} - ${this.point.percentage.toFixed(0)}%`
                        : `${this.point.y}`;
                    }
                  },
                  data: [],
                  color: stackingColorResolver.getSeriesColor({ stackId, seriesId })
                };
              }
              dealerIdToDealerFollowUpSeries[dealerId] = dealerFollowUpSeries;
              dealerFollowUpSeries.data.push([Date.parse(representativeFollowUp.date), 1]);
            }

            if (
              representativeFollowUp.status &&
              (representativeFollowUp.status.value == 3 ||
                representativeFollowUp.status.value == 'converted')
            ) {
              let dealerConversionSeries = dealerIdToDealerConversionSeries[dealerId];
              if (converterUserIdMap[dealerId]) {
                if (!dealerConversionSeries) {
                  let converterUserName = converterUserIdMap[dealerId].name;
                  let seriesId = `leads-converted-by--${dealerId}`;
                  let stackId = 'leads-converted-by-individual';
                  dealerConversionSeries = {
                    type: 'column',
                    name: `Leads Converted by "${converterUserName}"`,
                    id: seriesId,
                    stack: stackId,
                    stacking: 'normal',
                    tooltipOptions: {
                      split: false
                    },
                    dataLabels: {
                      enabled: true,
                      formatter() {
                        return this.point.percentage
                          ? `${this.point.y} - ${this.point.percentage.toFixed(0)}%`
                          : `${this.point.y}`;
                      }
                    },
                    data: [],
                    color: stackingColorResolver.getSeriesColor({ stackId, seriesId })
                  };
                }
                dealerIdToDealerConversionSeries[dealerId] = dealerConversionSeries;
                dealerConversionSeries.data.push([Date.parse(representativeFollowUp.date), 1]);
              }
            }
          });

          /**
           * Highcharts doesn't like un-sorted data, but it will never say it,
           * instead it will silently render the series' time incorrectly.
           * Waste my 70 minutes, Biatch!
           *
           * Here we'll sort the data to make it happy.
           */
          each(
            [
              ...values(dealerIdToDealerFollowUpSeries),
              ...values(dealerIdToDealerConversionSeries)
            ],
            series => {
              series.data = series.data.sort(([time1], [time2]) => time1 - time2);
              chartConfig.series.push(series);
            }
          );

          /** Debug * */
          console.log(chartConfig);
          window.setChartConfig = setChartConfig;
          window.chartConfig = chartConfig;

          setChartConfig({ ...chartConfig });
        })
        .finally(handleSpinner);
    },
    getNumOfDaysBetweenFromDateAndToDate: ({ input }) => () => {
      if (input.fromDate && input.toDate) {
        return daysBetween(input.fromDate, input.toDate);
      }

      return 0;
    }
  }),
  lifecycle({
    componentWillMount() {
      this.props.setChartConfig(
        {
          time: { timezoneOffset: new Date().getTimezoneOffset() },
          legend: { enabled: true },
          rangeSelector: {
            selected: 1
          },
          chart: {
            type: 'area'
          },
          title: {
            text: 'Funnel Analytics for the Whole Team'
          },
          tooltip: {
            //  pointFormat: '<span style="color:{series.color}">{series.name}</span>: <b>{point.percentage:.1f}%</b> ({point.y:,.0f} millions)<br/>',
            split: false
          },
          xAxis: {
            ordinal: false
          },
          yAxis: {
            stackLabels: {
              enabled: true,
              allowOverlap: true
            }
          },
          plotOptions: {
            // area: {
            //  stacking: 'percent',
            //  lineColor: '#ffffff',
            //  lineWidth: 1,
            //  marker: {
            //    lineWidth: 1,
            //    lineColor: '#ffffff'
            //  }
            // }
            series: {
              dataGrouping: {
                approximation: 'sum',
                forced: true,
                groupAll: true,
                units: [['hour', [1]]]
              }
            }
          },
          series: []
        },
        () => {
          this.props.handleFillInputFromUrl();
          setTimeout(this.props.handleSubmit);
        }
      );
    }
  })
)(SalesAnalytics);

/**
 * TODO share this function or use momentjs
 */
const daysBetween = (() => {
  const one_day = 1000 * 60 * 60 * 24;
  return (date1, date2) => {
    // Get 1 day in milliseconds

    // Convert both dates to milliseconds
    let date1_ms = new Date(date1).getTime();
    let date2_ms = new Date(date2).getTime();

    // Calculate the difference in milliseconds
    let difference_ms = date2_ms - date1_ms;

    // Convert back to days and return
    return Math.round(difference_ms / one_day);
  };
})();



