import React from 'react';
import PropTypes from 'prop-types';
import ControlForm from '../../_components/HOC/ControlForm';

import {
  Row,
  Col,
  Form,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Label,
  Input,
  CardFooter,
  Button
} from 'reactstrap';
import { compose, withHandlers } from 'recompose';

const Search = ({
  input, handleInput, handleSubmit, searchQuery, setSearchQuery
}) => (
  <Row>
    <Col md="12">
      <Form onSubmit={handleSubmit}>
        <Card>
          <CardHeader>
            <h3>Search</h3>
          </CardHeader>
          <CardBody>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Search: </Label>
                  <Input
                    type="text"
                    name="district"
                    placeholder="Enter id or name or status"
                    value={input.district || ''}
                    onChange={handleInput('district')}
                  />
                </FormGroup>
              </Col>
            </Row>
          </CardBody>
          <CardFooter>
            <Button type="submit" size="md" className="pull-left" color="primary">
              <i className="fa fa-dot-circle-o" /> Search
            </Button>
          </CardFooter>
        </Card>
      </Form>
    </Col>
  </Row>
);

export default compose(
  ControlForm,
  withHandlers({
    handleSubmit: ({
      setData, refresh, setWhereQuery, input
    }) => e => {
      e.preventDefault();
      debugger;
      if (input.district && input.district !== '') {
        setWhereQuery(
          {
            $or: [
              { _id: { $regex: `.*${input.district}.*`, $options: 'i' } },
              { name: { $regex: `.*${input.district}.*`, $options: 'i' } },
              { rental_status: { $regex: `.*${input.district}.*`, $options: 'i' } }
            ]
          },
          () => {
            refresh();
          }
        );
      } else {
        setWhereQuery({}, () => {
          refresh();
        });
      }
    }
  })
)(Search);



