import React from 'react';
import {
  Button,
  Input,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Col,
  FormGroup,
  Label
} from 'reactstrap';
import { compose, withHandlers } from 'recompose';
import ControlForm from '../_components/HOC/ControlForm';
import ControlSpinner from '../_components/HOC/ControlSpinner';
import { Spinner } from '../_components/common';
import toast from '../utils/toast';
import { postData } from '../utils/apiCall';
import ReduxCredential from './HOC/ReduxCredential';

import RentalStatusSelect from '../_components/RentalStatusSelect';

const VisitModal = ({
  input, handleInput, handleSelect, addVisit, isOpen, toggle, spinner
}) => (
  <Modal isOpen={isOpen} toggle={toggle}>
    <ModalHeader toggle={toggle}>Visit</ModalHeader>
    <ModalBody>
      <FormGroup row>
        <Label sm={2}>Room Id:</Label>
        <Col sm={10}>
          <Input type="text" value={input.roomId || ''} onChange={handleInput('roomId')} />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Arrange Date:</Label>
        <Col sm={10}>
          <Input
            type="date"
            value={input.arrangeDate || ''}
            onChange={handleInput('arrangeDate')}
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Visit Date:</Label>
        <Col sm={10}>
          <Input type="date" value={input.visitDate || ''} onChange={handleInput('visitDate')} />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Note:</Label>
        <Col sm={10}>
          <Input
            type="textarea"
            rows={10}
            value={input.note || ''}
            onChange={handleInput('note')}
          />
        </Col>
      </FormGroup>
    </ModalBody>
    <ModalFooter>
      {spinner ? (
        <Spinner />
      ) : (
        <Button color="primary" onClick={addVisit}>
          Submit
        </Button>
      )}
    </ModalFooter>
  </Modal>
);

export default compose(
  ReduxCredential,
  ControlSpinner,
  withHandlers({
    addVisit: ({
      input,
      updateCard,
      credential: { userId },
      cacheUserId,
      index,
      handleSpinner,
      toggle,
      response,
      setInput
    }) => () => {
      const {
        rental_status, location_detail, price, move_in_date, note
      } = input;

      handleSpinner();
      postData({
        route: '/user_visitings/addVisit',
        data: {
          userId: cacheUserId,
          data: input
        }
      })
        .then(data => {
          response[0].data[index].rental_status = data.data.rental_status;
          response[0].data[index].location_detail = data.data.location_detail;
          response[0].data[index].price = data.data.price;
          response[0].data[index].move_in_date = data.data.move_in_date;
          response[0].data[index].note = data.data.note;
          updateCard({
            loading: false,
            error: false,
            response
          });
          toast.success('Succesfully!!');
          // setInput({});
        })
        .catch(({ message }) => toast.error(message))
        .finally(() => {
          toggle();
          handleSpinner();
        });
    }
  })
)(VisitModal);



