import React from 'react';
import {
  Button,
  Input,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Col,
  FormGroup,
  Label
} from 'reactstrap';
import { withState, compose, withHandlers } from 'recompose';
import ControlForm from '../_components/HOC/ControlForm';
import ControlSpinner from '../_components/HOC/ControlSpinner';
import { Spinner } from '../_components/common';
import toast from '../utils/toast';
import { postData } from '../utils/apiCall';
import ReduxCredential from './HOC/ReduxCredential';

import ListingValueSelect from '../_components/ListingValueSelect';

const RoomNoteModal = ({
  input,
  handleInput,
  handleSelect,
  updateNoteRoom,
  isOpen,
  toggle,
  spinner
}) => (
  <Modal isOpen={isOpen} toggle={toggle}>
    {/* {console.log(input)} */}
    <ModalHeader toggle={toggle}>Note</ModalHeader>
    <ModalBody>
      <FormGroup row>
        <Label sm={2}>Available Status:</Label>
        <Col sm={10}>
          <ListingValueSelect
            value={input.available_status}
            onChange={handleSelect('available_status')}
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label sm={2}>Note:</Label>
        <Col sm={10}>
          <Input
            type="textarea"
            rows={10}
            value={input.note || ''}
            onChange={handleInput('note')}
          />
        </Col>
      </FormGroup>
    </ModalBody>
    <ModalFooter>
      {spinner ? (
        <Spinner />
      ) : (
        <Button color="primary" onClick={updateNoteRoom}>
          Submit
        </Button>
      )}
    </ModalFooter>
  </Modal>
);

export default compose(
  withState('selectedOption', 'handleChange', {}),
  ReduxCredential,
  ControlSpinner,
  withHandlers({
    updateNoteRoom: ({
      input,
      updateCard,
      credential: { userId },
      roomId,
      index,
      handleSpinner,
      toggle,
      response,
      setInput
    }) => () => {
      // const { note } = input;

      handleSpinner();
      postData({
        route: '/rooms/updateNoteRoom',
        data: {
          Id: roomId,
          data: input
        }
      })
        .then(data => {
          debugger;
          response[0].data[index].note = data.data.note;
          response[0].data[index].available_status = data.data.available_status;
          response[0].data[index].available_status_date = data.data.available_status_date;
          updateCard({
            loading: false,
            error: false,
            response
          });
          toast.success('Succesfully!!');
        })
        .catch(({ message }) => toast.error(message))
        .finally(() => {
          toggle();
          handleSpinner();
        });
    }
  })
)(RoomNoteModal);



