import React from 'react';
import Select from 'react-select';
import { compose } from 'recompose';

import { rentalStatusValue } from '../config';
import ReduxCredential from '../_components/HOC/ReduxCredential';
import ControlSpinner from '../_components/HOC/ControlSpinner';

const RentalStatusSelect = ({ value, onChange, isDisabled }) => (
  <Select value={value} onChange={onChange} options={rentalStatusValue} isDisabled={isDisabled} />
);

export default compose(
  ReduxCredential,
  ControlSpinner
)(RentalStatusSelect);



