import { compose, withState, lifecycle } from 'recompose';

import WithSpinnerError from '../HOC/SpinnerError';

export default apiCalls =>
  compose(
    withState('data', 'setData', { loading: true, error: false }),
    lifecycle({
      componentDidMount() {
        apiCalls
          .call(this)
          .then(values => {
            this.props.setData({
              loading: false,
              error: false,
              page: 0,
              response: values
            });
          })
          .catch(e =>
            this.props.setData({
              loading: false,
              error: true,
              e
            }));
      }
    }),
    WithSpinnerError
  );



