import React from "react";
import PropTypes from "prop-types";
import {
  Row,
  Col,
  Button,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  FormGroup,
  FormFeedback,
  Label,
  Input
} from "reactstrap";
import { Spinner } from "../common";
import Form from './BaseForm';

const UserForm = ({
  spinner,
  input,
  handleInput,
  validation,
  handleSubmit
}) => (
  <Row>
    <Col xs="12" sm="12">
      <Form onSubmit={handleSubmit}>
        <Card>
          <CardHeader>
            <h3>User Detail</h3>
          </CardHeader>
          <CardBody>
            <Row>
              <Col xs="12" sm="12" md="6" lg="6">
                <FormGroup>
                  <Label>First Name: </Label>
                  <Input
                    type="text"
                    name="firstName"
                    placeholder="Enter First Name"
                    value={input.firstName || ""}
                    onChange={handleInput("firstName")}
                    valid={validation.firstName === undefined}
                  />
                  <FormFeedback>{validation.firstName}</FormFeedback>
                </FormGroup>
              </Col>
              <Col xs="12" sm="12" md="6" lg="6">
                <FormGroup>
                  <Label>Last Name: </Label>
                  <Input
                    type="text"
                    name="lastName"
                    placeholder="Enter Last Name"
                    value={input.lastName || ""}
                    onChange={handleInput("lastName")}
                    valid={validation.lastName === undefined}
                  />
                  <FormFeedback>{validation.lastName}</FormFeedback>
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Real Name: </Label>
                  <Input
                    type="text"
                    name="realm"
                    placeholder="Enter Real Name"
                    value={input.realm || ""}
                    onChange={handleInput("realm")}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="6" lg="6">
                <FormGroup>
                  <Label>User Name: </Label>
                  <Input
                    type="text"
                    name="username"
                    placeholder="Enter User Name"
                    value={input.username || ""}
                    onChange={handleInput("username")}
                    valid={validation.username === undefined}
                  />
                  <FormFeedback>{validation.username}</FormFeedback>
                </FormGroup>
              </Col>
              <Col xs="12" sm="12" md="6" lg="6">
                <FormGroup>
                  <Label>Password: </Label>
                  <Input
                    type="text"
                    name="password"
                    placeholder="Enter Password"
                    value={input.password || ""}
                    onChange={handleInput("password")}
                    valid={validation.password === undefined}
                  />
                  <FormFeedback>{validation.password}</FormFeedback>
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Email: </Label>
                  <Input
                    type="text"
                    name="email"
                    placeholder="Enter Email"
                    value={input.email || ""}
                    onChange={handleInput("email")}
                    valid={validation.email === undefined}
                  />
                  <FormFeedback>{validation.email}</FormFeedback>
                </FormGroup>
              </Col>
            </Row>
          </CardBody>
          <CardFooter>
            {spinner ? (
              <Spinner />
            ) : (
              <React.Fragment>
                <Button type="submit" size="md" color="primary">
                  <i className="fa fa-dot-circle-o" /> Submit
                </Button>
              </React.Fragment>
            )}
          </CardFooter>
        </Card>
      </Form>
    </Col>
  </Row>
);

UserForm.defaultProps = {
  input: {}
};

UserForm.propTypes = {
  input: PropTypes.object,
  validation: PropTypes.object.isRequired,
  spinner: PropTypes.bool.isRequired,
  handleInput: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired
};
export default UserForm;



