import { compose, withState, withHandlers } from 'recompose';

import FetchData from '../../_components/HOC/FetchData';
import { getData, postData } from '../../utils/apiCall';
import UserVisitingList from '../../_components/List/UserVisitingList';
import ReduxCredential from '../../_components/HOC/ReduxCredential';
import toast from '../../utils/toast';
import { PAGE_NUMBER } from '../../config';
import ControlModal from '../../_components/HOC/ControlModal';
import ControlForm from '../../_components/HOC/ControlForm';

export default compose(
  withState('page', 'setPage', 0),
  withState('modalNote', 'setModalNote', { cacheUserId: '', currentIndex: 0 }),
  ReduxCredential,
  ControlModal('modal', 'setModal', false),
  ControlModal('modalVisit', 'setVisitModal', false),
  ControlForm,
  FetchData(function () {
    const { userId } = this.props.credential;
    return Promise.all([
      getData({
        route: '/user_visitings/userVisitingList',
        query: {
          filter: {
            limit: PAGE_NUMBER,
            skip: 0,
            where: {}
          }
        }
      }),
      getData({
        route: '/user_visitings/userVisitCount',
        query: {
          where: {}
        }
      })
    ]);
  })
)(UserVisitingList);



