import { compose, mapProps, withHandlers } from 'recompose';
import LeadForm from '../../_components/Form/LeadForm';

import FetchData from '../../_components/HOC/FetchData';
import ControlForm from '../../_components/HOC/ControlForm';
import ControlValidation from '../../_components/HOC/ControlValidation';
import ControlLeadForm from '../../_components/HOC/ControlLeadForm';
import ControlSpinner from '../../_components/HOC/ControlSpinner';

import { getData, patchData } from '../../utils/apiCall';
import LeadSchema from '../../formSchema/Lead';
import SubmitForm from '../../_components/HOC/SubmitForm';
import toast from '../../utils/toast';
import { postData } from '../../utils/apiCall';
import ReduxCredential from '../../_components/HOC/ReduxCredential';

export default compose(
  FetchData(function () {
    const { id } = this.props.match.params;
    return getData({ route: `/crm_leads/${id}` });
  }),
  mapProps(({ data: { response } }) => ({
    input: response.data
  })),
  ControlValidation,
  ControlForm,
  ControlSpinner,
  SubmitForm,
  ControlLeadForm,
  ReduxCredential,
  withHandlers({
    handleSubmit: ({ submitForm, input, handleSpinner }) => () => {
      submitForm({
        validationSchema: LeadSchema,
        apiFunction: patchData,
        apiPath: `/crm_leads/${input.id}`,
        handleSuccess: ({ data }) => {
          toast.success(`Lead "${data.name}" successfully updated`);
        },
        handleError: e => {
          toast.error(e.message);
        }
      });
    },
    assignDealer: ({ credential: { userId }, input, setInput }) => ({ leadId, index }) => {
      postData({
        route: '/crm_users/assignDealer',
        data: {
          userId,
          leadId
        }
      })
        .then(() => {
          toast.success('Successfully assigned dealer!!');
          input.dealerId = userId;
          setInput({ input });
        })
        .catch(({ response }) => {
          const { message } = response.data.error;
          if (message === 'Cannot assign!! Lead already has a dealer') {
            // input.dealerId = userId;
            // setInput({ input });
          }
          toast.error(message);
        });
    }
  })
)(LeadForm);



