import React from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Button,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  FormGroup,
  FormFeedback,
  Label,
  Input
} from 'reactstrap';
import { Spinner } from '../common';
import Form from './BaseForm';

const LeadForm = ({
  spinner,
  input,
  handleInput,
  validation,
  handleSubmit,
  checkDuplicate,
  assignDealer
}) => (
  <Row>
    <Col xs="12" sm="12">
      <Form onSubmit={handleSubmit}>
        <Card>
          <CardHeader>
            <h3>Lead Detail</h3>
          </CardHeader>
          <CardBody>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Name: </Label>
                  <Input
                    type="text"
                    name="name"
                    placeholder="Enter name"
                    value={input.name || ''}
                    onChange={handleInput('name')}
                    valid={validation.name === undefined}
                  />
                  <FormFeedback>{validation.name}</FormFeedback>
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Post Link: </Label>
                  <Input
                    type="text"
                    name="postLink"
                    placeholder="Enter post link"
                    value={input.postLink || ''}
                    onChange={handleInput('postLink')}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="12" lg="12">
                <FormGroup>
                  <Label>Profile Link: </Label>
                  <Input
                    type="text"
                    name="profileLink"
                    placeholder="Enter profile link"
                    value={input.profileLink || ''}
                    onChange={handleInput('profileLink')}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="4" lg="4">
                <FormGroup>
                  <Label>Phone: </Label>
                  <Input
                    type="text"
                    name="rawPhone"
                    placeholder="Enter address"
                    value={input.rawPhone || ''}
                    onChange={handleInput('rawPhone')}
                  />
                </FormGroup>
              </Col>
              <Col xs="12" sm="12" md="4" lg="4">
                <FormGroup>
                  <Label>District: </Label>
                  <Input
                    type="text"
                    name="district"
                    placeholder="Enter district"
                    value={input.district || ''}
                    onChange={handleInput('district')}
                    valid={validation.district === undefined}
                  />
                  <FormFeedback>{validation.district}</FormFeedback>
                </FormGroup>
              </Col>
              <Col xs="12" sm="12" md="4" lg="4">
                <FormGroup>
                  <Label>Source: </Label>
                  <Input
                    type="text"
                    name="source"
                    placeholder="Enter source"
                    value={input.source || ''}
                    onChange={handleInput('source')}
                    valid={validation.source === undefined}
                  />
                  <FormFeedback>{validation.source}</FormFeedback>
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col xs="12" sm="12" md="6" lg="6">
                <FormGroup>
                  <Label>Post info:</Label>
                  <Input
                    type="textarea"
                    name="postInfo"
                    placeholder="Enter post info"
                    value={input.postInfo || ''}
                    onChange={handleInput('postInfo')}
                    style={{ height: 200 }}
                  />
                </FormGroup>
              </Col>
              <Col xs="12" sm="12" md="6" lg="6">
                <FormGroup>
                  <Label>Hunt Note:</Label>
                  <Input
                    type="textarea"
                    name="note"
                    placeholder="Enter note"
                    value={input.note || ''}
                    onChange={handleInput('note')}
                    style={{ height: 200 }}
                  />
                </FormGroup>
              </Col>
            </Row>
          </CardBody>
          <CardFooter>
            {spinner ? (
              <Spinner />
            ) : (
              <React.Fragment>
                <Button type="submit" size="md" className="pull-left" color="primary">
                  <i className="fa fa-dot-circle-o" /> Submit
                </Button>

                <a
                  onClick={checkDuplicate}
                  size="md"
                  className="btn text-white btn-primary pull-left ml-2"
                >
                  <i className="fa fa-check" /> Check Duplicate
                </a>

                {input.id && !input.dealerId ? (
                  <a
                    onClick={() => {
                      console.log('hhehehe');
                      assignDealer({ leadId: input.id, index: 0 });
                    }}
                    size="md"
                    className="btn text-white btn-primary pull-left ml-2"
                  >
                    <i className="fa fa-plus-square" /> Assign Me
                  </a>
                ) : null}
              </React.Fragment>
            )}
          </CardFooter>
        </Card>
      </Form>
    </Col>
  </Row>
);

LeadForm.defaultProps = {
  input: {}
};

LeadForm.propTypes = {
  input: PropTypes.object,
  validation: PropTypes.object.isRequired,
  spinner: PropTypes.bool.isRequired,
  handleInput: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired
};
export default LeadForm;



