import { compose, withHandlers } from 'recompose';
import { postData, getData, deleteData } from '../../utils/apiCall';
import ReduxCredential from '../../_components/HOC/ReduxCredential';
import toast from '../../utils/toast';
import FetchData from '../../_components/HOC/FetchData';
import DealList from '../../_components/List/DealList';

import { PAGE_NUMBER } from '../../config';

export default compose(
  FetchData(() =>
    Promise.all([
      getData({
        route: '/crm_leads',
        query: {
          filter: {
            include: 'dealer',
            where: { dealerId: { exists: false } },
            order: 'createAt ASC',
            limit: PAGE_NUMBER,
            skip: 0
          }
        }
      }),
      getData({
        route: '/crm_leads/count',
        query: {
          where: { dealerId: { exists: false } }
        }
      })
    ])),
  ReduxCredential,
  withHandlers({
    assignDealer: ({ credential: { userId }, data, setData }) => ({ leadId, index }) => {
      postData({
        route: '/crm_users/assignDealer',
        data: {
          userId,
          leadId
        }
      })
        .then(() => {
          toast.success('Successfully assigned dealer!!');
          const newData = { ...data }; // after assign then total will be increase 1
          newData.response[0].data.splice(index, 1);
          newData.response[1].data.count -= 1;
          setData(newData);
        })
        .catch(({ response }) => {
          const { message } = response.data.error;
          if (message === 'Cannot assign!! Lead already has a dealer') {
            const newData = { ...data }; // after assign then total will be increase 1
            newData.response[0].data.splice(index, 1);
            newData.response[1].data.count -= 1;
            setData(newData);
          }
          toast.error(message);
        });
    },
    deleteLead: ({ credential: { userId }, data, setData }) => ({ leadId, index }) => {
      deleteData({
        route: `/crm_leads/${leadId}`
      })
        .then(() => {
          toast.success('Successfully delete lead!!');
          const newData = { ...data }; // after assign then total will be increase 1
          newData.response[0].data.splice(index, 1);
          newData.response[1].data.count -= 1;
          setData(newData);
        })
        .catch(({ response }) => {
          // const { message } = response.data.error;
          // if (message === 'Cannot assign!! Lead already has a dealer') {
          //   const newData = { ...data }; // after assign then total will be increase 1
          //   newData.response[0].data.splice(index, 1);
          //   newData.response[1].data.count -= 1;
          //   setData(newData);
          // }
          // toast.error(message);
        });
    }
  })
)(DealList);



