import { withState, compose, withHandlers } from "recompose";

import FetchData from "../../_components/HOC/FetchData";
import { getData } from "../../utils/apiCall";
import User from "../../_components/List/User";

// export default FetchData(() => getData({ route: "/crm_users" }))(User);

const PAGE_NUMBER = 10;

export default compose(
  withState("page", "setPage", 0),
  FetchData(
    () =>
      getData({
        route: "/crm_users",
        query: {
          filter: {
            order: "createAt ASC",
            limit: PAGE_NUMBER,
            skip: 0
          }
        }
      }),
    () =>
      getData({
        route: "/crm_users/count"
      })
  ),
  withHandlers({
    reloadDataWithPage: ({ setData, data }) => page => {
      getData({
        route: "/crm_users",
        query: {
          filter: {
            order: "createAt ASC",
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER
          }
        }
      })
        .then(response =>
          setData({
            loading: false,
            error: false,
            response,
            total: data.total
          })
        )
        .catch(e =>
          setData({
            loading: false,
            error: true,
            e
          })
        );
    }
  }),
  withHandlers({
    nextPage: ({ page, setPage, reloadDataWithPage }) => () => {
      reloadDataWithPage(page + 1);
      setPage(page + 1);
    },
    prevPage: ({ page, setPage, reloadDataWithPage }) => () => {
      reloadDataWithPage(page - 1);
      setPage(page - 1);
    }
  })
)(User);



