import React, { Component } from "react";
import PropTypes from "prop-types";
import { Row, Button, Table, Col } from "reactstrap";

import { getData } from "../../utils/apiCall";
import createPaginate from "../HOC/createCacheUserPaginate";
import { PAGE_NUMBER } from "../../config";

import NoteModal from "./../NoteModal";
import VisitModal from "./../VisitModal";
import dayjs from "dayjs";

const Pagination = createPaginate({
  apiCalls(page, userId, whereQuery) {
    return Promise.all([
      getData({
        route: "/cache_users/userList",
        query: {
          filter: {
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER,
            where: { ...whereQuery }
          }
        }
      }),
      getData({
        route: "/cache_users/userCount",
        query: {
          where: { ...whereQuery }
        }
      })
    ]);
  }
});
class CacheUserList extends Component {
  render() {
    const {
      data: { response },
      setData,
      setModal,
      modal,
      setVisitModal,
      modalVisit,
      modalNote,
      input,
      setInput,
      handleInput,
      handleSelect,
      setModalNote,
      ...props
    } = this.props;
    const [
      leads,
      {
        data: { count }
      }
    ] = response;
    return (
      <Row className="animated fadeIn">
        <Col md="12">
          <Pagination setData={setData} count={count} {...props} />
          <Table striped>
            <thead>
              <tr>
                <th>#</th>
                <th>User ID</th>
                <th>Name</th>
                <th>Last Show Up</th>
                {/* <th>District</th>
                <th>App Versions</th> */}
                <th>Facebook Link</th>
                <th>Status</th>
                <th>Location</th>
                <th>Price</th>
                <th>Move-in-date</th>
                <th style={{ width: "200" }}>Note</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {leads.data.map((lead, index) => (
                <tr key={lead._id}>
                  <th scope="row">{index + 1}</th>
                  <td>{lead._id}</td>
                  <td>{lead.name}</td>
                  <td>
                    {dayjs(new Date(lead.lastShowUpTime)).format("DD/MM/YYYY")}
                  </td>
                  {/* <td />
                  <td /> */}
                  <td>
                    {lead.link !== undefined ? (
                      <a target="_blank" href={lead.link}>
                        {"Link"}
                      </a>
                    ) : (
                      ""
                    )}
                  </td>
                  <td>{lead.rental_status}</td>
                  <td>{lead.location_detail}</td>
                  <td>{lead.price}</td>
                  <td>{dayjs(lead.move_in_date).format("DD/MM/YYYY")}</td>
                  <td>
                    <pre style={{ whiteSpace: "pre-wrap" }}>{lead.note}</pre>
                  </td>
                  <td>
                    <Button
                      size="sm"
                      color="link"
                      onClick={() => {
                        setModalNote({
                          cacheUserId: lead._id,
                          currentIndex: index
                        });
                        setInput(
                          {
                            rental_status: {
                              value: lead.rental_status,
                              label: lead.rental_status
                            },
                            location_detail: lead.location_detail,
                            price: lead.price,
                            move_in_date: dayjs(lead.move_in_date).format(
                              "YYYY-MM-DD"
                            ),
                            note: ""
                          },
                          () => setVisitModal(true)
                        );
                      }}
                    >
                      Visit <i className="icon-pencil" />
                    </Button>

                    <Button
                      size="sm"
                      color="link"
                      onClick={() => {
                        setModalNote({
                          cacheUserId: lead._id,
                          currentIndex: index
                        });
                        setInput(
                          {
                            rental_status: {
                              value: lead.rental_status,
                              label: lead.rental_status
                            },
                            location_detail: lead.location_detail,
                            price: lead.price,
                            move_in_date: dayjs(lead.move_in_date).format(
                              "YYYY-MM-DD"
                            ),
                            note: lead.note
                          },
                          () => setModal(true)
                        );
                      }}
                    >
                      Update <i className="icon-pencil" />
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>

          <NoteModal
            input={input}
            isOpen={modal}
            toggle={() => setModal(!modal)}
            cacheUserId={modalNote.cacheUserId}
            index={modalNote.currentIndex}
            updateCard={setData}
            response={response}
            handleInput={handleInput}
            handleSelect={handleSelect}
          />

          <VisitModal
            input={input}
            isOpen={modalVisit}
            toggle={() => setVisitModal(!modalVisit)}
            cacheUserId={modalNote.cacheUserId}
            index={modalNote.currentIndex}
            updateCard={setData}
            response={response}
            handleInput={handleInput}
            handleSelect={handleSelect}
          />
        </Col>
      </Row>
    );
  }
}

CacheUserList.propTypes = {
  data: PropTypes.object.isRequired
};
export default CacheUserList;



