import React from 'react';
import PropTypes from 'prop-types';
import { Row, Button, Table, Col } from 'reactstrap';

import { getData } from '../../utils/apiCall';
import createPaginate from '../HOC/createPaginate';
import { PAGE_NUMBER } from '../../config';

import { getDealNote, getListingValue } from '../../utils/lead';

import { pathOr } from 'ramda';

import dayjs from 'dayjs';
import FollowUpModal from './../FollowUpModal';

const Pagination = createPaginate({
  apiCalls(page, userId, whereQuery) {
    return Promise.all([
      getData({
        route: `/crm_users/${userId}/crmDeal`,
        query: {
          filter: {
            include: 'dealer',
            order: 'schedule DESC, createAt DESC',
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER,
            where: whereQuery
          }
        }
      }),
      getData({ route: `/crm_users/${userId}/crmDeal/count`, query: { where: whereQuery }})
    ]);
  }
});

const DealLeadMe = ({
  data: { response }, setData, releaseDealer, listingDealer, ...props, showPopup, toggleModal, isModalOpen, modalFollow
}) => {
  const [
    leads,
    {
      data: { count }
    }
  ] = response;
  return (
    <Row className="animated fadeIn">
      <Col md="12">
      <Pagination setData={setData} count={count} {...props} isRoom={false}/>
      <Table striped>
        <thead>
          <tr>
            <th>#</th>
            <th>Landlord ID</th>
            <th>Landlord name</th>
            <th>Phone number</th>
            <th>District</th>
            <th>Source</th>
            {/* <th>Hunter</th> */}
            <th colSpan="2">Deal status</th>

            <th>Deal Note</th>
            {/* <th>Listing</th> */}
            <th>Deal date</th>
            <th>Dealer</th>
            <th colSpan="3">Action</th>
          </tr>
        </thead>
        <tbody>
          {leads.data.map((lead, index) => (
            // <LeadCard key={lead.id} input={lead} />
            <tr key={lead.id}>
              <th scope="row">{index + 1}</th>
              <td>
                <a target="_blank" href={lead.profileLink}>
                  {lead.id.substr(0, 5)}
                </a>
              </td>
              <td>
                  <a target="_blank" href={lead.postLink}>
                    {lead.name}
                  </a>
              </td>
              <td>{lead.rawPhone}</td>
              <td>{lead.district}</td>
              <td>{lead.source}</td>
              {/* <td>Phưn</td> */}
              <td>{lead.status.label}</td>
              <td>
                <Button size="sm" color="link" onClick={() => {
                    showPopup({ leadId: lead.id, index, kindModal: 0 })
                  }}>
                  Add <i className="icon-plus" />
                </Button>
              </td>
              <td>{getDealNote(lead.followUp)}</td>
              {/* <td>{getListingValue(lead.followUp)}</td> */}
              <td>{lead.dealDate ? dayjs(lead.dealDate).format('DD/MM/YYYY') : ''}</td>
              <td>{pathOr('', ['dealer', 'username'], lead)}</td>
              <td>
                <a href={`#/master/edit_lead/${lead.id}`}>
                  Edit <i className="icon-pencil" />
                </a>
              </td>
              <td>
                {(lead.isListing !== undefined && lead.isListing === false) ? <Button
                  color="link"
                  size="sm"
                  onClick={() => {
                    listingDealer({ leadId: lead.id, index });
                  }}
                >
                  Listing <i className="fa fa-minus" />
                </Button>:""
                }
                
              </td>
              <td>
                <Button
                  color="link"
                  size="sm"
                  onClick={() => {
                    releaseDealer({ leadId: lead.id, index });
                  }}
                >
                  Release Me <i className="fa fa-minus" />
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      <FollowUpModal
        kindModal={modalFollow.kindModal}
        isOpen={isModalOpen}
        toggle={()=>toggleModal(!isModalOpen)}
        leadId={modalFollow.leadId}
        index={modalFollow.currentIndex}
        updateCard={setData}
        response={response}
      />
      </Col>
    </Row>
  );
};

DealLeadMe.propTypes = {
  data: PropTypes.object.isRequired,
  releaseDealer: PropTypes.func.isRequired
};
export default DealLeadMe;



