import React from 'react';
import { Typeahead } from 'react-bootstrap-typeahead';
import { compose, withState, withHandlers, mapProps } from 'recompose';
import {
  Row, Button, Table, Col,
  Form, Input, FormGroup,
  ListGroup, ListGroupItem,
  Card, CardBody
} from 'reactstrap';
import FetchData from '../../../_components/HOC/FetchData';
import ControlForm from '../../../_components/HOC/ControlForm';
import { getData } from '../../../utils/apiCall';
import removeDiacritics from '../../../utils/string/removeDiacritics';

const RoomSearch = ({
  districts, handleSubmitSearchRooms,
  selectDistricts,
  matchedLeads
  }) => (
  <Row className="pt-4 h-100">
    <Col className="d-flex flex-column">
      <Form>
        <FormGroup>
          <Typeahead
            labelKey="text"
            multiple={true}
            options={districts}
            onChange={selectDistricts}
            placeholder="Choose multiple districts..."
          />
        </FormGroup>
        <FormGroup>
          <Button
            type="submit"
            size="lg"
            color="primary"
            onClick={handleSubmitSearchRooms}
          >Search Rooms</Button>
        </FormGroup>
      </Form>
      <div style={{overflow:'scroll'}}>
        <Card>
          <CardBody>
            <h3>Listed on App</h3>
            <p>todo: Search inventory, converted leads, new leads by District</p>
            <ListGroup>
              {
                [0,0,0,0].map(()=>(
                  <ListGroupItem>Cras justo odio</ListGroupItem>
                ))
              }
            </ListGroup>
          </CardBody>
        </Card>

        {
          //<Card>
          //  <CardBody>
          //    <h3>Converted but not Listed</h3>
          //    <p>
          //      todo: what should the workflow be between demandSupport and dealer?
          //    </p>
          //    <ListGroup>
          //      {
          //        [0,0,0,0].map(()=>(
          //          <ListGroupItem>Cras justo odio</ListGroupItem>
          //        ))
          //      }
          //    </ListGroup>
          //  </CardBody>
          //</Card>
        }

        <Card>
          <CardBody>
            <h3>Leads</h3>
            <p>
              todo: what should the workflow be between demandSupport and dealer?
            </p>
            <ListGroup>
              {
                matchedLeads.map((lead)=>(
                  <ListGroupItem>
                    <p>
                      <strong>
                        {lead.id} {lead.name}
                      </strong>
                    </p>
                    <p>
                      <span>
                        {' '}district:{lead.district}
                      </span>
                      <p>
                        {' '}status:{lead.status ? lead.status.label : 'New'}
                      </p>
                    </p>
                  </ListGroupItem>
                ))
              }
            </ListGroup>

          </CardBody>
        </Card>


        <p>
          todo: request crawl new
        </p>

      </div>
    </Col>
  </Row>
);

export default compose(
  FetchData(() => getData({route:'/locale_districts'})),
  mapProps(({ data: { response: { data: districts } }, ...otherProps }) => ({
    districts,
    ...otherProps
  })),
  //withState('matchedNewLeads','setMatchedNewLeads',[]),
  withState('matchedLeads','setMatchedLeads',[]),
  withState('matchedConvertedLeads','setMatchedConvertedLeads',[]),
  ControlForm,
  withHandlers({
    selectDistricts: ({input,setInput})=>(districts)=>{
      setInput({...input, districts:districts})
    },
    searchLeads: ({input})=>()=>{

      let formattedDistrictFilters = (input.districts || []).map((district)=>{
        return removeDiacritics(district.text)
          .toLowerCase()
          .replace(/(quan |huyen )/,'')
          .replace(/\s/g,'_')

      });

      return getData({route:'/crm_leads',query:{filter:{
        where: {
          district: {inq: formattedDistrictFilters}
        }
      }}})
    }
  }),
  withHandlers({
    handleSubmitSearchRooms: ({
      searchLeads,
      setMatchedLeads
    })=>()=>{
      searchLeads()
        .then(({data:matchedLeads}) => {
          //todo: separate converted, declined, followUp, listed, and new leads
          matchedLeads.sort(({status:status1},{status:status2})=>{
            return (status1 ? status1.value : 0) -
              (status2 ? status2.value : 0)
          });
          setMatchedLeads(matchedLeads);
        });
    }
  })
)(RoomSearch)



