import React from 'react';
import PropTypes from 'prop-types';

import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import { compose, withState, withHandlers } from 'recompose';
import { PAGE_NUMBER } from '../../config';
import Search from './createCacheUserSearch';

const Paginate = ({
  page, count, prev, next, current, whereQuery, setWhereQuery, refresh
}) => {
  const total = Array(Math.ceil(count / PAGE_NUMBER)).fill(0);

  let startPage = page - 5;
  let endPage = page + 4;
  if (startPage <= 0) {
    endPage -= startPage - 1;
    startPage = 1;
  }
  if (endPage > total) {
    endPage = total;
    if (endPage > total) {
      startPage = endPage - 9;
    }
  }

  return (
    <React.Fragment>
      <Search whereQuery={whereQuery} setWhereQuery={setWhereQuery} refresh={refresh} />
      <Pagination>
        <PaginationItem disabled={page === 0}>
          <PaginationLink previous onClick={prev}>
            Prev
          </PaginationLink>
        </PaginationItem>
        {total.map((d, index) => {
          if (index >= startPage - 1 && index <= endPage - 1) {
            return (
              <PaginationItem active={index === page} key={index}>
                <PaginationLink
                  onClick={() => {
                    current(index);
                  }}
                >
                  {index + 1}
                </PaginationLink>
              </PaginationItem>
            );
          }
        })}
        <PaginationItem disabled={count <= (page + 1) * PAGE_NUMBER}>
          <PaginationLink next onClick={next}>
            Next
          </PaginationLink>
        </PaginationItem>
      </Pagination>
    </React.Fragment>
  );
};

Paginate.propTypes = {
  page: PropTypes.number.isRequired,
  count: PropTypes.number.isRequired,
  next: PropTypes.func.isRequired,
  prev: PropTypes.func.isRequired,
  current: PropTypes.func.isRequired
};

export default ({ apiCalls }) =>
  compose(
    withState('whereQuery', 'setWhereQuery', {}),
    withState('page', 'setPage', 0),
    withHandlers({
      reloadDataWithPage: ({ setPage, setData, credential: { userId } = { userId: '' } }) => (
        page,
        whereQuery
      ) => {
        setPage(page);
        apiCalls
          .call(this, page, userId, whereQuery)
          .then(response => {
            setData({
              loading: false,
              error: false,
              page: parseInt(page, 10),
              response
            });
          })
          .catch(e =>
            setData({
              loading: false,
              error: true,
              e
            }));
      }
    }),
    withHandlers({
      next: ({ page, whereQuery, reloadDataWithPage }) => () => {
        reloadDataWithPage(page + 1, whereQuery);
      },
      prev: ({ page, whereQuery, reloadDataWithPage }) => () => {
        reloadDataWithPage(page - 1, whereQuery);
      },
      current: ({ whereQuery, reloadDataWithPage }) => currentPage => {
        reloadDataWithPage(currentPage, whereQuery);
      },
      refresh: ({ whereQuery, reloadDataWithPage }) => () => {
        reloadDataWithPage(0, whereQuery);
      }
    })
  )(Paginate);



