import React from 'react';
import {
  Button,
  Input,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Col,
  FormGroup,
  Label
} from 'reactstrap';
import { compose, withHandlers } from 'recompose';
import ControlForm from '../_components/HOC/ControlForm';
import ControlSpinner from '../_components/HOC/ControlSpinner';
import StatusSelect from '../_components/StatusSelect';
import { Spinner } from '../_components/common';
import toast from '../utils/toast';
import { patchData } from '../utils/apiCall';
import ReduxCredential from './HOC/ReduxCredential';
import DealValueSelect from '../_components/DealValueSelect';
import ListingValueSelect from '../_components/ListingValueSelect';
import DeclinedReasonSelect from '../_components/DeclinedReasonSelect';

const FollowUpModal = ({
  input,
  handleInput,
  handleSelect,
  addFollowup,
  isOpen,
  toggle,
  spinner,
  kindModal,
  updateListing
}) => (
  <Modal isOpen={isOpen} toggle={toggle}>
    {kindModal === 0 ? (
      <div>
        <ModalHeader toggle={toggle}>Follow-up Note</ModalHeader>
        <ModalBody>
          <FormGroup row>
            <Label sm={2}>Status:</Label>
            <Col sm={10}>
              <StatusSelect input={input.status || {}} onChange={handleSelect('status')} />
            </Col>
          </FormGroup>
          {input.status &&
            input.status.value === 3 && (
              <FormGroup row>
                <Label sm={2}>Deal Value:</Label>
                <Col sm={10}>
                  <Input
                    type="text"
                    value={input.dealValue || ''}
                    onChange={handleInput('dealValue')}
                  />
                  {/* <DealValueSelect value={input.dealValue} onChange={handleSelect('dealValue')} /> */}
                </Col>
              </FormGroup>
            )}

          {input.status &&
            (input.status.value === 1 || input.status.value === 2) && (
              <FormGroup row>
                <Label sm={2}>Schedule:</Label>
                <Col sm={10}>
                  <Input
                    type="date"
                    value={input.schedule || ''}
                    onChange={handleInput('schedule')}
                  />
                </Col>
              </FormGroup>
            )}
          {input.status &&
            input.status.value === 4 && (
              <FormGroup row>
                <Label sm={2}>Declined Reason:</Label>
                <Col sm={10}>
                  <DeclinedReasonSelect
                    value={input.declinedReasonValue}
                    onChange={handleSelect('declinedReasonValue')}
                  />
                </Col>
              </FormGroup>
            )}
          <FormGroup row>
            <Label sm={2}>Note:</Label>
            <Col sm={10}>
              <Input
                type="textarea"
                rows={5}
                value={input.followUp || ''}
                onChange={handleInput('followUp')}
              />
            </Col>
          </FormGroup>
        </ModalBody>
        <ModalFooter>
          {spinner ? (
            <Spinner />
          ) : (
            <Button color="primary" onClick={addFollowup}>
              Submit
            </Button>
          )}
        </ModalFooter>
      </div>
    ) : (
      <div>
        <ModalHeader toggle={toggle}>Listing</ModalHeader>
        <ModalBody>
          <FormGroup row>
            <Label sm={2}>Listing Value:</Label>
            <Col sm={10}>
              <ListingValueSelect
                value={input.listingValue}
                onChange={handleSelect('listingValue')}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={2}>Url:</Label>
            <Col sm={10}>
              <Input
                type="text"
                value={input.listingUrl || ''}
                onChange={handleInput('listingUrl')}
              />
            </Col>
          </FormGroup>
        </ModalBody>
        <ModalFooter>
          {spinner ? (
            <Spinner />
          ) : (
            <Button color="primary" onClick={updateListing}>
              Submit
            </Button>
          )}
        </ModalFooter>
      </div>
    )}
  </Modal>
);

export default compose(
  ReduxCredential,
  ControlSpinner,
  ControlForm,
  withHandlers({
    addFollowup: ({
      input,
      updateCard,
      credential: { userId },
      leadId,
      index,
      handleSpinner,
      toggle,
      response,
      setInput
    }) => () => {
      const {
        status, followUp, dealValue, schedule
      } = input;

      if (!status && !followUp) {
        // toast.warn('Please fill in information!!');
        if (status.value === 3 && !dealValue) {
          toast.warn('Please fill in deal value!!');
          return;
        }
      }

      if (status.value === 1 || status.value === 2) {
        if (!schedule) {
          toast.warn('Please fill in schedule!!');
          return;
        }
      }

      handleSpinner();
      patchData({
        route: '/crm_leads/addFollowup',
        data: { userId, leadId, data: input }
      })
        .then(({ data: { data } }) => {
          // debugger;
          response[0].data[index].status = data.status;
          response[0].data[index].dealDate = data.dealDate;
          response[0].data[index].status = data.status;
          updateCard({
            loading: false,
            error: false,
            response
          });

          toast.success('Succesfully added follow up!!');
          setInput({});
        })
        .catch(({ message }) => toast.error(message))
        .finally(() => {
          toggle();
          handleSpinner();
        });
    },
    updateListing: ({
      input,
      updateCard,
      credential: { userId },
      leadId,
      index,
      handleSpinner,
      toggle,
      response,
      setInput
    }) => () => {
      const { status, followUp, dealValue } = input;
      // if (!status && !followUp) {
      //   toast.warn('Please fill in information!!');
      //   if (status.value === 3 && !dealValue) {
      //     toast.warn('Please fill in deal value!!');
      //   }
      // }
      handleSpinner();
      patchData({
        route: '/crm_leads/updateListing',
        data: { userId, leadId, data: input }
      })
        .then(({ data: { data } }) => {
          // debugger;
          response[0].data[index].listing = data.listing;
          // response[0].data[index].followUp = data.followUp;
          updateCard({
            loading: false,
            error: false,
            response
          });

          toast.success('Succesfully added follow up!!');
          setInput({});
        })
        .catch(({ message }) => toast.error(message))
        .finally(() => {
          toggle();
          handleSpinner();
        });
    }
  })
)(FollowUpModal);



