import { compose } from 'recompose';
import FetchData from '../../_components/HOC/FetchData';
import { getData } from '../../utils/apiCall';
import Lead from '../../_components/List/Lead';
import { PAGE_NUMBER } from '../../config';

export default compose(FetchData(() =>
  Promise.all([
    getData({
      route: '/crm_leads',
      query: {
        filter: {
          include: 'dealer',
          order: 'createAt ASC',
          limit: PAGE_NUMBER,
          skip: 0
        }
      }
    }),
    getData({
      route: '/crm_leads/count'
    })
  ])))(Lead);



