import React, { Component } from 'react';

import { compose, withState, lifecycle } from 'recompose';
import { getData } from '../utils/apiCall';

import { Card, CardBody, Progress, Row, Col } from 'reactstrap';
import classNames from 'classnames';
import { mapToCssModules } from 'reactstrap/lib/utils';

class Widget01 extends Component {
  render() {
    const {
      className,
      cssModule,
      header,
      mainText,
      smallText,
      color,
      value,
      children,
      variant,
      ...attributes
    } = this.props;

    // demo purposes only
    const progress = { style: '', color, value };
    const card = { style: '', bgColor: '' };

    if (variant === 'inverse') {
      progress.style = 'progress-white';
      progress.color = '';
      card.style = 'text-white';
      card.bgColor = `bg-${color}`;
    }

    const classes = mapToCssModules(classNames(className, card.style, card.bgColor), cssModule);
    progress.style = classNames('progress-xs my-3', progress.style);

    return (
      <Card className={classes} {...attributes}>
        <CardBody>
          <div className="h4 m-0">{header}</div>
          <div>{mainText}</div>
          <Progress className={progress.style} color={progress.color} value={progress.value} />
          <small className="text-muted">{smallText}</small>
          <div>{children}</div>
        </CardBody>
      </Card>
    );
  }
}

const Full = props => (
  <Row>
    <Col md="3">
      <Widget01
        color="info"
        variant="inverse"
        header={props.dashBoard.countTotalView}
        mainText="Lead Total"
      />
    </Col>
    <Col md="3">
      <Widget01
        color="info"
        variant="inverse"
        header={props.dashBoard.countView}
        mainText="Lead New"
      />
    </Col>
    <Col md="3">
      <Widget01
        color="warning"
        variant="inverse"
        header={props.dashBoard.countContact}
        mainText="Lead Contact"
      />
    </Col>
    <Col md="3">
      <Widget01
        color="success"
        variant="inverse"
        header={props.dashBoard.countConverted}
        mainText="Lead Converted"
      />
    </Col>
  </Row>
);

export default compose(
  withState('dashBoard', 'setDashBoard', { countView: 0, countContact: 0, countConverted: 0 }),
  lifecycle({
    componentDidMount() {
      let c0 = getData({
        route: '/crm_leads/count'
      });

      let c1 = getData({
        route: '/crm_leads/count',
        query: {
          where: { 'status.value': 0 }
        }
      });

      let c2 = getData({
        route: '/crm_leads/count',
        query: {
          where: { 'status.value': { ne: 0 } }
        }
      });

      let c3 = getData({
        route: '/crm_leads/count',
        query: {
          where: { 'status.value': 3 }
        }
      });

      Promise.all([c0, c1, c2, c3]).then(values => {
        this.props.setDashBoard({
          countTotalView: values[0].data.count.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ','),
          countView: values[1].data.count.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ','),
          countContact: values[2].data.count.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ','),
          countConverted: values[3].data.count.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
        });
      });
    }
  })
)(Full);



