import { compose, mapProps, withHandlers } from "recompose";
import UserForm from "../../_components/Form/UserForm";

import FetchData from "../../_components/HOC/FetchData";
import ControlForm from "../../_components/HOC/ControlForm";
import ControlValidation from "../../_components/HOC/ControlValidation";
import ControlSpinner from "../../_components/HOC/ControlSpinner";

import { getData, patchData } from "../../utils/apiCall";
import UserSchema from "../../formSchema/Lead";
import SubmitForm from "../../_components/HOC/SubmitForm";
import toast from "../../utils/toast";

export default compose(
  FetchData(function() {
    const { id } = this.props.match.params;
    return getData({ route: `/crm_users/${id}` });
  }),
  mapProps(({ data: { response } }) => ({
    input: response.data
  })),
  ControlValidation,
  ControlForm,
  ControlSpinner,
  SubmitForm,
  withHandlers({
    handleSubmit: ({ submitForm, input }) => e => {
      e.preventDefault();
      submitForm({
        validationSchema: UserSchema,
        apiFunction: patchData,
        apiPath: `/crm_users/${input.id}`,
        handleSuccess: ({ data }) => {
          toast.success(`User "${data.realm}" successfully updated`);
        },
        handleError: e => {
          toast.error(JSON.stringify(e));
        }
      });
    }
  })
)(UserForm);



