import { compose, withState, withHandlers,withProps } from 'recompose';
const queryString = require('qs');

export default compose(
  /**
   * todo: this might not be the best place to put this here, might need some sort of utils
   */
  withProps(({}) => ({
    chartTickIntervalOptions: {
      'by-10-minute': { label: 'By 10 Minutes', tickIntervalDef: [['minute', [10]]] },
      'by-30-minute': { label: 'By 30 Minutes', tickIntervalDef: [['minute', [30]]] },
      hourly: { label: 'Hourly', tickIntervalDef: [['hour', [1]]] },
      '3-hourly': { label: '3-Hourly', tickIntervalDef: [['hour', [3]]] },
      daily: { label: 'Daily', tickIntervalDef: [['day', [1]]] },
      weekly: { label: 'Weekly', tickIntervalDef: [['week', [1]]] },
      monthly: { label: 'Monthly', tickIntervalDef: [['month', [1]]] },
      quarterly: { label: 'Quarterly', tickIntervalDef: [['month', [3]]] }
    }
  })),
  withState('chartConfig', 'setChartConfig', {}),
  withHandlers({
    handleApplyTickIntervalToChart: ({
      chartConfig,
      setChartConfig,
      chartTickIntervalOptions
      }) => newChartTickIntervalCode => {
      let chartTickIntervalOption = chartTickIntervalOptions[newChartTickIntervalCode];
      if (chartTickIntervalOption) {
        chartConfig.plotOptions.series.dataGrouping.units = chartTickIntervalOption.tickIntervalDef;
        setChartConfig({ ...chartConfig });
      }
    }
  }),
  withHandlers({
    translateFromDateToTimeStamp: () => fromDate => {
      let fromDate_nativeDate = fromDate ? new Date(fromDate) : new Date();
      fromDate_nativeDate.setHours(0);
      return fromDate_nativeDate.getTime();
    },
    translateToDateToTimeStamp: () => toDate => {
      let toDate_nativeDate = toDate ? new Date(toDate) : new Date();
      toDate_nativeDate.setHours(24);
      return toDate_nativeDate.getTime();
    },
    translateFromDateToISOString: () => fromDate => {
      let fromDate_nativeDate = fromDate ? new Date(fromDate) : new Date();
      fromDate_nativeDate.setHours(0);
      return fromDate_nativeDate.toISOString();
    },
    translateToDateToISOString: () => toDate => {
      let toDate_nativeDate = toDate ? new Date(toDate) : new Date();
      toDate_nativeDate.setHours(24);
      return toDate_nativeDate.toISOString();
    },
    handleSaveInputToUrl: ({ history, input }) => () => {
      history.push({
        search: `?${queryString.stringify(input)}`
      });
    },
    handleFillInputFromUrl: ({ location, setInput, handleApplyTickIntervalToChart }) => (config = {}) => {
      let input = queryString.parse(location && location.search, {
        ignoreQueryPrefix: true
      });

      /** todo when charting gets more complex, this logic might be changed/moved */
      handleApplyTickIntervalToChart(input.chartTickInterval);

      setInput(input);
    },

    handleSelectTickInterval: ({ input, setInput, handleApplyTickIntervalToChart }) => ({
      target: { value: newTickInterval }
      }) => {
      input.chartTickInterval = newTickInterval;
      setInput({ ...input });

      handleApplyTickIntervalToChart(newTickInterval);
    }

  })
)


