import React from "react";
import PropTypes from "prop-types";
import {
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Table,
  Pagination,
  PaginationItem,
  PaginationLink
} from "reactstrap";

const User = ({
  data: { response, total },
  nextPage,
  prevPage,
  page,
  history
}) => (
  <Row className="animated fadeIn">
    <Col>
      <Card>
        <CardHeader>
          <i className="fa fa-align-justify" />
          <a
            className="btn btn-primary pull-right"
            role="button"
            href="#/master/new_user"
          >
            <i className="fa fa-plus-square" /> New User
          </a>
        </CardHeader>
        <CardBody>
          <Table hover bordered striped responsive size="sm">
            <thead>
              <tr>
                <th>User ID</th>
                <th>First Name</th>
                <th>Last Name</th>
                <th>Real Name</th>
                <th>User Name</th>
                <th>Email</th>
                {/*
                  <th>Hunt date</th>
                  <th>Hunter Name</th>
                  <th>Hunter Team Member ID</th>
                */}
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {response.data.map(user => (
                <tr key={user.id}>
                  <td>{user.id}</td>
                  <td>{user.firstName}</td>
                  <td>{user.lastName}</td>
                  <td>{user.realm}</td>
                  <td>{user.username}</td>
                  <td>{user.email}</td>
                  <td>
                    <a href={`#/master/edit_user/${user.id}`}>
                      Edit <i className="icon-pencil" />
                    </a>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
          <Pagination>
            <PaginationItem disabled={page === 0}>
              <PaginationLink
                previous
                onClick={() => {
                  prevPage();
                }}
              >
                Prev
              </PaginationLink>
            </PaginationItem>
            <PaginationItem disabled={total <= (page + 1) * 10}>
              <PaginationLink
                next
                onClick={() => {
                  nextPage();
                }}
              >
                Next
              </PaginationLink>
            </PaginationItem>
          </Pagination>
        </CardBody>
      </Card>
    </Col>
  </Row>
);

User.propTypes = {
  data: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
};
export default User;



