import React from 'react';
import PropTypes from 'prop-types';
import {
  compose,
  withHandlers,
  withState,
  withProps,
  mapProps,
  lifecycle,
  withStateHandlers
} from 'recompose'
import {
  Row,
  Col,
  Button,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  FormGroup,
  FormFeedback,
  ButtonDropdown,
  DropdownToggle,
  DropdownItem,
  DropdownMenu
} from 'reactstrap';


const ChartSeriesControl = ({
  dropDownIsOpen,
  setDropDownOpen,
  user,
  onRemove,
  setVisibility,
  label
  }) => (
  <ButtonDropdown
    className="mb-2 mr-2"
    direction="left"
    isOpen={dropDownIsOpen}
    toggle={() => {
        setDropDownOpen(!dropDownIsOpen);
      }}
  >
    <DropdownToggle caret color="primary" >
      {label}
    </DropdownToggle>
    <DropdownMenu>
      <DropdownItem onClick={onRemove}>Remove</DropdownItem>
    </DropdownMenu>
  </ButtonDropdown>
);

ChartSeriesControl.propTypes = {
  label: PropTypes.string.isRequired,
  onRemove: PropTypes.func.isRequired
};


export default compose(
  withState('dropDownIsOpen', 'setDropDownOpen', false)
)( ChartSeriesControl );



