import React from 'react';
import Select from 'react-select';
import { compose } from 'recompose';

import { dealValue } from '../config';
import ReduxCredential from '../_components/HOC/ReduxCredential';
import ControlSpinner from '../_components/HOC/ControlSpinner';

const StatusSelect = ({ value, onChange, isDisabled }) => (
  <Select value={value} onChange={onChange} options={dealValue} isDisabled={isDisabled} />
);

export default compose(
  ReduxCredential,
  ControlSpinner
)(StatusSelect);



