import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Button, Table, Col } from 'reactstrap';

import { getData } from '../../utils/apiCall';
import createPaginate from '../HOC/createUserVisitingPaginate';
import { PAGE_NUMBER } from '../../config';

import NoteModal from './../NoteModal';
import VisitModal from './../VisitModal';
import dayjs from 'dayjs';

const Pagination = createPaginate({
  apiCalls(page, userId, whereQuery) {
    return Promise.all([
      getData({
        route: '/user_visitings/userVisitingList',
        query: {
          filter: {
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER,
            where: { ...whereQuery }
          }
        }
      }),
      getData({
        route: '/user_visitings/userVisitCount',
        query: {
          where: { ...whereQuery }
        }
      })
    ]);
  }
});
class CacheUserList extends Component {
  render() {
    const {
      data: { response },
      setData,
      setModal,
      modal,
      setVisitModal,
      modalVisit,
      modalNote,
      input,
      setInput,
      handleInput,
      handleSelect,
      setModalNote,
      ...props
    } = this.props;
    const [
      leads,
      {
        data: { count }
      }
    ] = response;
    return (
      <Row className="animated fadeIn">
        <Col md="12">
          <Pagination setData={setData} count={count} {...props} />
          <Table striped>
            <thead>
              <tr>
                <th>#</th>
                <th>User ID</th>
                <th>Name</th>
                <th>Room Id</th>
                <th>Room Name</th>
                <th>Arrange Date</th>
                <th>Visit Date</th>
                <th>Note</th>
              </tr>
            </thead>
            <tbody>
              {leads.data.map((lead, index) => (
                <tr key={lead.cache_users._id}>
                  <th scope="row">{index + 1}</th>
                  <td>{lead.cache_users._id}</td>
                  <td>{lead.cache_users.name}</td>
                  <td>{lead.rooms._id}</td>
                  <td>{lead.rooms.room_name}</td>
                  <td>{dayjs(lead.arrangeDate).format('DD/MM/YYYY HH:mm')}</td>
                  <td>{dayjs(lead.visitDate).format('DD/MM/YYYY HH:mm')}</td>
                  <td>{lead.note}</td>
                </tr>
              ))}
            </tbody>
          </Table>

          {/* <NoteModal
            input={input}
            isOpen={modal}
            toggle={() => setModal(!modal)}
            cacheUserId={modalNote.cacheUserId}
            index={modalNote.currentIndex}
            updateCard={setData}
            response={response}
            handleInput={handleInput}
            handleSelect={handleSelect}
          />

          <VisitModal
            input={input}
            isOpen={modalVisit}
            toggle={() => setVisitModal(!modalVisit)}
            cacheUserId={modalNote.cacheUserId}
            index={modalNote.currentIndex}
            updateCard={setData}
            response={response}
            handleInput={handleInput}
            handleSelect={handleSelect}
          /> */}
        </Col>
      </Row>
    );
  }
}

CacheUserList.propTypes = {
  data: PropTypes.object.isRequired
};
export default CacheUserList;



