import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { Container } from 'reactstrap';

import Header from '../components/Header/';
import Sidebar from '../components/Sidebar/';
import Aside from '../components/Aside/';
// import Footer from '../components/Footer/';
import Breadcrumb from '../components/Breadcrumb/';

import NewUser from '../containers/User/NewUser';
import EditUser from '../containers/User/EditUser';
import ListUser from '../containers/User/ListUser';
import EventBrowser from '../containers/Analytics/EventBrowser';
import SalesAnalytics from '../containers/Analytics/SalesAnalytics';
import DataPlayground from '../containers/Analytics/DataPlayground';
import NewLead from '../containers/Lead/NewLead';
import EditLead from '../containers/Lead/EditLead';
import ListLead from '../containers/Lead/ListLead';

import DealList from '../containers/Deal/DealList';
import DealListMe from '../containers/Deal/DealListMe';

import ConvertedList from '../containers/Converted/ConvertedList';
import DemandSupport from '../containers/Demand/DemandSupport';

import DashBoard from '../_components/DashBoard';

import HotRoomList from '../containers/HotRoomList/HotRoomList';
import CacheUserList from '../containers/CacheUserList/CacheUserList';
import UserVisitingList from '../containers/CacheUserList/UserVisitingList';

const Full = props => (
  <div className="app">
    <Header />
    <div className="app-body">
      <Sidebar {...props} />
      <main className="main">
        <Breadcrumb />
        <Container fluid>
          <Switch>
            <Route exact path="/master/new_user" name="NewUser" component={NewUser} />
            <Route path="/master/edit_user/:id" name="EditUser" component={EditUser} />
            <Route exact path="/master/user_list" name="ListUser" component={ListUser} />
            <Route
              exact
              path="/master/event_browser"
              name="EventBrowser"
              component={EventBrowser}
            />
            <Route
              exact
              path="/master/sales_analytics"
              name="SalesAnalytics"
              component={SalesAnalytics}
            />
            <Route exact path="/master/dash_board" name="DashBoard" component={DashBoard} />
            <Route
              exact
              path="/master/data_playground"
              name="Data Playground"
              component={DataPlayground}
            />
            <Route exact path="/master/new_lead" name="NewLead" component={NewLead} />
            <Route path="/master/edit_lead/:id" name="EditLead" component={EditLead} />
            <Route exact path="/master/lead_list" name="ListLead" component={ListLead} />
            <Route exact path="/deal/list" name="DealList" component={DealList} />
            <Route exact path="/deal/me" name="DealListMe" component={DealListMe} />
            <Route exact path="/converted/list" name="ConvertedList" component={ConvertedList} />
            <Route exact path="/hotroom/list" name="HotRoomList" component={HotRoomList} />
            <Route exact path="/cacheuser/list" name="CacheUserList" component={CacheUserList} />
            <Route
              exact
              path="/cacheuser/uservisitinglist"
              name="UserVisitingList"
              component={UserVisitingList}
            />
            <Route exact path="/demand/support" name="DemandSupport" component={DemandSupport} />
            <Redirect from="/" to="/dashboard" />
          </Switch>
        </Container>
      </main>
      <Aside />
    </div>
    {
      // <Footer />
    }
  </div>
);

export default Full;



