export class StackingColorResolver {
  availableColors = [
    '#8bbc21',
    '#910000', '#1aadce', '#492970',
    '#f28f43', '#77a1e5', '#c42525',
    '#a6c96a'
  ];

  constructor() {
    this.currentColorPointer = 0;
    this.stackSeriesColorStore = {};
  }
  lightenColor(color, percent) {
    var num = parseInt(color.replace('#',''),16),
      amt = Math.round(2.55 * percent),
      R = (num >> 16) + amt,
      B = (num >> 8 & 0x00FF) + amt,
      G = (num & 0x0000FF) + amt;
    return '#' + (0x1000000 + (R<255?R<1?0:R:255)*0x10000 + (B<255?B<1?0:B:255)*0x100 + (G<255?G<1?0:G:255)).toString(16).slice(1);
  }
  getSeriesColor({seriesId, stackId}) {
    let seriesColorResolver = this.stackSeriesColorStore[stackId];
    if(!seriesColorResolver) {
      /** todo: might be able to generalize this into recursive class definitions (in the future)  */
      seriesColorResolver = {
        keynoteColor: this.availableColors[this.currentColorPointer],
        colors: [],
        seriesColorStore: {}
      };
      this.currentColorPointer = (this.currentColorPointer+1) % (this.availableColors.length-1);
      this.stackSeriesColorStore[stackId] = seriesColorResolver;
    }

    let seriesColor = seriesColorResolver.seriesColorStore[seriesId];
    if(!seriesColor) {
      seriesColor = seriesColorResolver.colors[seriesColorResolver.colors.length-1] ?
        this.lightenColor(seriesColorResolver.colors[seriesColorResolver.colors.length-1],15) :
        seriesColorResolver.keynoteColor;

      seriesColorResolver.colors.push(seriesColor);
      seriesColorResolver.seriesColorStore[seriesId] = seriesColor;
    }

    return seriesColor;
  }
}


