import React from 'react';
import PropTypes from 'prop-types';
import { Row, Button, Table, Col } from 'reactstrap';
import createPaginate from '../HOC/createPaginate';
import { PAGE_NUMBER } from '../../config';
import { getData } from '../../utils/apiCall';

import { getDealNote } from '../../utils/lead';

import { pathOr } from 'ramda';

import dayjs from 'dayjs';

const Pagination = createPaginate({
  apiCalls: (page, userId, whereQuery) =>
    Promise.all([
      getData({
        route: '/crm_leads',
        query: {
          filter: {
            include: 'dealer',
            where: { dealerId: { exists: false }, ...whereQuery },
            order: 'createAt ASC',
            limit: PAGE_NUMBER,
            skip: page * PAGE_NUMBER
          }
        }
      }),
      getData({
        route: '/crm_leads/count',
        query: {
          where: { dealerId: { exists: false }, ...whereQuery }
        }
      })
    ])
});

const Convert = ({
  data: { response }, setData, assignDealer, deleteLead, ...props
}) => {
  const [
    leads,
    {
      data: { count }
    }
  ] = response;
  return (
    <Row className="animated fadeIn">
      <Col md="12">
        <Pagination setData={setData} count={count} {...props} isRoom={false} />
        {/* {leads.data.map((lead, index) => (
        <LeadCard key={lead.id} input={lead}>
          <Button
            color="info"
            size="sm"
            onClick={() => {
              assignDealer({ leadId: lead.id, index });
            }}
          >
            <i className="fa fa-plus-square" /> Assign Me
          </Button>
        </LeadCard>
      ))} */}

        <Table striped>
          <thead>
            <tr>
              <th>#</th>
              <th>Landlord ID</th>
              <th>Landlord name</th>
              <th>Phone number</th>
              <th>District</th>
              <th>Source</th>
              {/* <th>Hunter</th> */}
              <th>Deal status</th>
              <th>Deal Note</th>
              <th>Deal date</th>
              <th>Dealer</th>
              <th colSpan="2">Action</th>
            </tr>
          </thead>
          <tbody>
            {leads.data.map((lead, index) => (
              // <LeadCard key={lead.id} input={lead} />
              <tr key={lead.id}>
                <th scope="row">{index + 1}</th>
                <td>
                  <a target="_blank" href={lead.profileLink}>
                    {lead.id.substr(0, 5)}
                  </a>
                </td>
                <td>
                  <a target="_blank" href={lead.postLink}>
                    {lead.name}
                  </a>
                </td>
                <td>{lead.rawPhone}</td>
                <td>{lead.district}</td>
                <td>{lead.source}</td>
                {/* <td>Phưn</td> */}
                <td>{lead.status.label}</td>
                <td>{getDealNote(lead.followUp)}</td>
                {/* <td>{dayjs(pathOr(null, ['dealDate'], lead)).format('DD/MM/YYYY')}</td> */}
                <td>{lead.dealDate ? dayjs(lead.dealDate).format('DD/MM/YYYY') : ''}</td>
                <td>{pathOr('', ['dealer', 'username'], lead)}</td>
                <td>
                  <a href={`#/master/edit_lead/${lead.id}`}>
                    Edit <i className="icon-pencil" />
                  </a>
                  <Button
                    color="link"
                    size="sm"
                    onClick={() => {
                      if (window.confirm('Are you sure you wish to delete this item?')) {
                        deleteLead({ leadId: lead.id, index });
                      }
                    }}
                  >
                    Delete <i className="fa fa-trash-o" />
                  </Button>
                </td>
                <td>
                  <Button
                    color="link"
                    size="sm"
                    onClick={() => {
                      assignDealer({ leadId: lead.id, index });
                    }}
                  >
                    Assign Me <i className="fa fa-plus-square" />
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Col>
    </Row>
  );
};

Convert.propTypes = {
  data: PropTypes.object.isRequired,
  assignDealer: PropTypes.func.isRequired,
  setData: PropTypes.func.isRequired
};
export default Convert;



